import { CorePackage } from '../tools/package';
import { Tree } from '../tools/tree';
import { CoreString } from '../utils/string';
import { CategoryId, ICategory } from '../dto/category.dto';
import { CoreTypes } from '../utils/typelevel';

export namespace CategoryQuery {
  import toTagName = CoreString.toTagName;
  import Maybe = CoreTypes.Maybe;
  import Package = CorePackage.Package;
  import flatten3 = CorePackage.flatten3;

  export const searchCategoriesById
    : (id: Package<CategoryId>, includeTop?: boolean) => (top: Package<Tree<ICategory>>) => Package<Maybe<Tree<ICategory>>>
    = (id, includeTop: boolean = true) => (top) =>
    top.map(t => t.find('DepthFirst'))
      .zip(id)
      .map(([find, id]) => find(n => n.id === id));

  export const searchCategoriesByName
    : (search: Package<string>, includeTop?: boolean) => (top: Package<Tree<ICategory>>) => Package<ICategory[]>
    = (search, includeTop: boolean = true) => (top) =>
      flatten3(
        top.map(t => t.filter('DepthFirst')),
        search,
        top,
      )
      .map(([filter, s, t]) =>
        filter(
          n =>
            (!(includeTop && t.node.map(topN => topN.id !== n.id).getOrElse(false)))
              &&
            toTagName(`${n.name.default} ${n.name.full} ${n.name.slug}`).search(toTagName(s)) >= 0
        )
      );

  export const searchCategoriesBySlug
    : (slug: Package<string>, includeTop?: boolean) => (top: Package<Tree<ICategory>>) => Package<Maybe<Tree<ICategory>>>
    = (slug, includeTop: boolean = true) => (top) =>
    top.map(t => t.find('DepthFirst'))
      .zip(slug)
      .map(([find, slug]) => find(n => n.name.slug === slug));
}
