









import Vue from 'vue';
import { Component, Prop } from 'vue-property-decorator';
import UIText from '@/components/ui/text/UIText.vue';
import { CoreUuid } from '@findit/core';
import uuid = CoreUuid.uuid;

@Component({
  components: {UIText}
})
export default class UITextField extends Vue {
  @Prop({ default: `ui-text-field-${uuid()}` }) protected id!: string;
  @Prop({ default: '' }) protected label!: string;
  @Prop({ default: '' }) protected preset!: string;
}
