























import { Component } from 'vue-property-decorator';
import { Route } from 'vue-router';
import {
  CategoryQuery,
  CoreLens,
  ICategory,
  IService,
  CorePackage,
  ServiceQuery,
  Tree,
  CategoryId,
  DataBus
} from '@findit/core';
import globalStore, { storeCategoryTreeLens, storeServicesLens } from '@/store';
import LogoHeader from '@/components/ui/app/LogoHeader.vue';
import ObjectDebug from '@/components/debug/ObjectDebug.vue';
import UIList from '@/components/ui/list/UIList.vue';
import { VueRouterExt } from '@/router/vue-router-ext';
import UISearchBox from '@/components/ui/search-box/UISearchBox.vue';
import searchCategoriesById = CategoryQuery.searchCategoriesById;
import searchServicesByCategory = ServiceQuery.searchServicesByCategory;
import searchServicesByName = ServiceQuery.searchServicesByName;
import and2M = CoreLens.and2M;
import Package = CorePackage.Package;
import toRawDataBus = VueRouterExt.toRawDataBus;
import toPackagedDataBus = DataBus.toPackagedDataBus;
import unwrapMaybe = CorePackage.unwrapMaybe;
import emptyTree = Tree.emptyTree;
import { View } from '@/components/ui/app/view';
import lift = CorePackage.lift;
import nodeG = Tree.nodeG;
import CategoryTree from '@/components/category/CategoryTree.vue';
import childrenG = Tree.childrenG;

export const browseViewResolver = (to: Route, from: Route, next: any): void => {
  const categoryId = DataBus.GetterLenses.categoryId.get(
    toPackagedDataBus(toRawDataBus(to.query))
  );

  and2M(
    DataBus.MutableLenses.categoryTree,
    DataBus.MutableLenses.services,
  ).setM(to.meta, [
    categoryId.isDefined()
      ? searchCategoriesById(
        categoryId as Package<CategoryId>,
        false
      )(storeCategoryTreeLens.get(globalStore))
      : storeCategoryTreeLens.get(globalStore),
    storeServicesLens.get(globalStore),
  ]);
  next();
};

// TODO: Finish and move to core
// export function copyQueryToMeta<T, U extends { query: T, meta: any }>(to: U): Task<U & { meta: T }> {
//   // new Lens('query').get(to)
//   //return Package.lift(to)
//   //  .map()
// }

@Component({
  components: {
    CategoryTree,
    UISearchBox,
    ObjectDebug,
    LogoHeader,
    UIList,
  },
  beforeRouteEnter: browseViewResolver,
  beforeRouteUpdate: browseViewResolver,
})
export default class BrowseView extends View<IService[]> {
  protected categoriesSearch: string = '';
  protected servicesSearch: string = '';

  get categoryTree(): Package<Tree<ICategory>> {
    if (DataBus.GetterLenses.categoryTree.get(this.dataBus).isDefined()) {
      return DataBus.GetterLenses.categoryTree.get(this.dataBus).map(unwrapMaybe(emptyTree()))
    }
    throw new Error('Browse: "categoryTree" is not defined on the data bus');
  }

  get allServices(): Package<IService[]> {
    if (DataBus.GetterLenses.services.get(this.dataBus).isDefined()) {
      return DataBus.GetterLenses.services.get(this.dataBus).map(unwrapMaybe([] as IService[]))
    }
    throw new Error('Browse: "services" is not defined on the data bus');
  }

  get model(): IService[] {
    return this.categoryTree.isDefined()
      ? this.categoryTree
        .flatMap<IService[]>(c => {
          return !!c
            ? searchServicesByCategory(this.categoryTree.map(c => c as Tree<ICategory>))(this.allServices)
            : lift([])
        })
        .flatMap<IService[]>(svcs => {
          return !!this.servicesSearch
            ? searchServicesByName(lift(this.servicesSearch))(lift(svcs))
            : lift(svcs)
        })
        .consume()
      : [];
  }

  get showCategory(): boolean {
    return this.categoryTree.map(t => t.parent.isDefined()).consume();
  }

  get category(): ICategory {
    return this.categoryTree.map(nodeG<ICategory>().get).consume();
  }

  get subCategories(): ICategory[] {
    return this.categoryTree
      .map(t =>
        t.children.map(cs => cs.map(nodeG<ICategory>().get).toArray()
        ).getOrElse([] as ICategory[]))
      .consume()
      // .map(childrenG<ICategory>().get)
      // .map(cs => cs.map(nodeG<ICategory>.get))
      // .consume();
  }

  get isEmpty(): boolean {
    return this.model.length < 1;
  }

  protected onCategoriesSearch(s: string): void {
    this.categoriesSearch = s;
  }

  protected onServicesSearch(s: string): void {
    this.servicesSearch = s;
  }
}

export const BrowseRoute = VueRouterExt.route({
  name: 'browse',
  path: 'browse',
  component: BrowseView,
  meta: {
    back: true
  }
});

