import * as faker from 'faker';
import { CoreRandom } from '../../src/utils/random';
import { CoreUuid, IListing, IPublicUser, ITopic, levelsOfService, CorePackage } from '../../src';

export module MockListing {
  import randomBetween = CoreRandom.randomBetween;

  export const random
    : (u: IPublicUser | IPublicUser[]) => IListing
    = (u) => ({
      id: CoreUuid.uuid(),
      createdBy: u instanceof Array ? faker.random.arrayElement(u) : u,
      createdOn: faker.date.recent(),
      asap: faker.random.boolean(),
      dateTime: faker.date.future(),
      los: faker.random.arrayElement(levelsOfService),
      meta: undefined,
    })

  export const randomList
    : (x: number) => (up: IPublicUser[]) => IListing[]
    = (x) => (userPool) =>
      (Array(x).fill(undefined).map(_ => random(faker.random.arrayElement(userPool))));

}
