import { Map } from 'immutable';

export namespace CoreConfig {

  export type MinMaxConfig = {
    min: number;
    max: number;
  };

  export type FixedSizeConfig = {
    size: number;
  };

  export type Parameters = Partial<MinMaxConfig & FixedSizeConfig>;

  export const isFixedSizeConfig: (configParams: Parameters) => boolean
    = c => !isNaN(Number((c as FixedSizeConfig).size));
  // export declare function isFixedSizeConfig<T extends Parameters>(configParams: T): T extends FixedSizeConfig ? true : false;

  export const isMinMaxConfig: (configParams: Parameters) => boolean
    = c => !isNaN(Number((c as MinMaxConfig).max));

  export const decrementConfigParams: (configParams: Parameters) => Parameters
    = c => Map(c as Parameters).map(v => !!v ? --v : v).toObject() as Parameters;

}
