import { RouteConfig } from 'vue-router';
import { Vue } from 'vue/types/vue';
import { CategoryId, CoreString, CoreTypes, DataBus, ListingId, PersonId, ServiceId } from '@findit/core';
import { Dictionary } from 'vue-router/types/router';

export namespace VueRouterExt {
  import toTagName = CoreString.toTagName;
  import nothing = CoreTypes.nothing;

  export type VueQueryAndParams = Dictionary<string | (string | null)[]>;

  export type RouteParams<C> = {
    name: string,
    component: C,
    meta?: any,
    path?: string,
  };

  export const route
    : <C extends typeof Vue>(params: RouteParams<C>) => RouteConfig
    = ({ name, component, meta, path }) => {
    return {
      name: `${name}`,
      path: `/${path ? path : toTagName(name)}`,
      component: component,
      meta: {
        ...meta
      }
    };
  };

  export const toRawDataBus
    : (qp: VueQueryAndParams) => DataBus.Raw
    = (qp) => ({
    categoryId: qp['categoryId'] as CategoryId,
    listingId: qp['listingId'] as ListingId,
    serviceId: qp['serviceId'] as ServiceId,
    userId: qp['userId'] as PersonId,
    category: nothing,
    categories: nothing,
    categoryTree: nothing,
    categorySlug: qp['categorySlug'] as string,
    user: nothing,
    service: nothing,
    services: nothing,
    listing: nothing,
    listings: nothing,
  });
}
