
























// @ is an alias to /src
import { Component } from 'vue-property-decorator';
import { Route } from 'vue-router';
import { ICategory, CorePackage, Tree, DataBus } from '@findit/core';
import globalStore, { storeCategoryTreeLens } from '@/store';
import { View } from '@/components/ui/app/view';
import LogoHeader from '@/components/ui/app/LogoHeader.vue';
import CategoryTree from '@/components/category/CategoryTree.vue';
import ObjectDebug from '@/components/debug/ObjectDebug.vue';
import { VueRouterExt } from '@/router/vue-router-ext';
import UIText from '@/components/ui/text/UIText.vue';
import Package = CorePackage.Package;
import emptyTree = Tree.emptyTree;


export const homeResolver = (to: Route, from: Route, next: any): void => {
  DataBus.MutableLenses.categoryTree.setM(to.meta, storeCategoryTreeLens.get(globalStore));
  next();
};

@Component({
  components: {
    UIText,
    ObjectDebug,
    LogoHeader,
    CategoryTree
  },
  beforeRouteEnter: homeResolver,
})
export default class HomeView extends View<Package<Tree<ICategory>>> {

  protected get model() {
    return DataBus.GetterLenses.categoryTree
            .get(this.dataBus)
            .orElse(() => emptyTree());
  }

  protected get tree(): Tree<ICategory> {
    return this.model.consume();
  }

  protected get hasChildren(): boolean {
    return this.model
      .map(ct => ct.children.map(c => c.count() > 0))
      .map(hasChildren => hasChildren.getOrElse(false))
      .consume();
  }
}

export const HomeRoute = VueRouterExt.route({
  name: 'home',
  component: HomeView,
});

