












import { Component, Vue, Prop } from 'vue-property-decorator';
import { CoreString, CoreUuid } from '@findit/core';
import { Button } from '@/components/ui/button/button';
import UIText from '@/components/ui/text/UIText.vue';
import toTagName = CoreString.toTagName;
import uuid = CoreUuid.uuid;

@Component({
  components: {UIText}
})
export default class UIButton extends Vue {
  @Prop({ default: `ui-button-${uuid()}` }) protected id!: string;
  @Prop({ default: '' }) protected text!: string;
  @Prop({ default: 'IDLE' }) protected defaultState!: string;
  @Prop({ default: false }) protected runOnce!: boolean;
  @Prop({ default: () => Promise.resolve() }) protected task!: <T>() => Promise<T>;

  protected toTagName = toTagName;
  protected currentState!: Button.TState;

  constructor() {
    super();
    this.currentState = this.defaultState as Button.TState;
  }

  protected get stateClassModifier(): string {
    return toTagName(this.currentState);
  }

  protected get state(): Button.TState {
    return this.currentState;
  }

  protected clearState() {
    return this.setState(this.defaultState as Button.TState);
  }

  protected setState(s: Button.TState) {
    return () => {
      this.currentState = s;
    };
  }

  protected onClick() {
    // TODO: convert to reactive
    this.setState('PROCESSING')();
    return this.task()
      .then(this.setState('COMPLETE'), this.setState('ERROR'))
        .finally(() => {
          setTimeout((this.runOnce) ? this.setState('DISABLED') : this.clearState(), 5000);
          return;
        });

  }

}
