import Vue from 'vue';
import VueRouter, { RouteConfig } from 'vue-router';

import PageNotFoundView from '@/views/404.vue';
import { HomeRoute } from '@/views/Home.vue';
import { CategoryRoute } from '@/views/Category.vue';
import { KitchenSinkRoute } from '@/views/KitchenSink.vue';
import { SearchCategoriesRoute } from '@/views/SearchCategories.vue';
import { JobRequestRoute } from '@/views/JobRequest.vue';
import { BrowseRoute } from '@/views/Browse.vue';
import { CreateJobRequestRoute } from '@/views/CreateJobRequest.vue';

Vue.use(VueRouter);

const routes: RouteConfig[] = [
  HomeRoute,
  CategoryRoute,
  KitchenSinkRoute,
  SearchCategoriesRoute,
  BrowseRoute,
  JobRequestRoute,
  CreateJobRequestRoute,
  {
    path: '*',
    component: PageNotFoundView
  }
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  fallback: true,
});

export default router;
