import { CoreString } from '../utils/string';
import { CorePackage } from '../tools/package';
import { Tree } from '../tools/tree';
import { ICategory } from '../dto/category.dto';
import { IService, ServiceId } from '../dto/service.dto';
import { CoreTypes } from '../utils/typelevel';
import { PersonId } from '../dto/person.dto';

export namespace ServiceQuery {
  import toTagName = CoreString.toTagName;
  import Maybe = CoreTypes.Maybe;
  import Package = CorePackage.Package;

  export const serviceBelongsToCategory
    : (t: Tree<ICategory>) => (s: IService) => boolean
    = t => s => t.traverseBreadthFirst(false)(([n, b]) =>
      (b || s.category === Tree.nodeG<ICategory>().get(n).id)
  );

  export const searchServicesByCategory
    : (c: Package<Tree<ICategory>>) => (ss: Package<IService[]>) => Package<IService[]>
    = (c) => (ss) =>
      ss.zip(c)
        .map(([ss, c]) =>
          ss.filter(serviceBelongsToCategory(c)));

  export const searchServicesByName
    : (s: Package<string>) => (ss: Package<IService[]>) => Package<IService[]>
    = (s) => (ss) =>
      ss.zip(s)
        .map(([ss, search]) =>
          ss.filter(svc =>
            toTagName(`${svc.name.full} ${svc.name.default}`)
              .search(toTagName(search)) >= 0
          ));

  export const searchServicesById
    : (id: Package<ServiceId>) => (ss: Package<IService[]>) => Package<Maybe<IService>>
    = (id) => (ss) =>
      ss.zip(id).map(([ss, sId]) => ss.find(svc => svc.id === sId));

  export const searchServicesByProvider
    : (p: Package<PersonId>) => (ss: Package<IService[]>) => Package<IService[]>
    = (p) => (ss) =>
      ss.zip(p).map(([ss, user]) => ss.filter(s => s.provider === user))
}
