import * as faker from 'faker';
import { CoreString } from '../../src/utils/string';
import { CoreRandom } from '../../src/utils/random';
import { IPublicUser } from '../../src/dto/user.dto';
import { ITopic } from '../../src/dto/topic.dto';
import { CorePackage } from '../../src/tools/package';
import { CoreUuid } from '../../src/utils/uuid';

export module MockTopic {

  import toTagName = CoreString.toTagName;
  import randomBetween = CoreRandom.randomBetween;

  export const random
    : (p: IPublicUser[]) => ITopic
    = (userPool) =>
    CorePackage.lift(faker.random.word())
      .map(name => ({
        id: CoreUuid.uuid(),
        name: {
          default: toTagName(name),
          short: name,
          full: faker.random.words(randomBetween(5, 10))
        },
        createdBy: faker.random.arrayElement(userPool)
      } as ITopic))
      .consume();

  export const randomList
    : (x: number) => (p: IPublicUser[]) => ITopic[]
    = (x) => (userPool) =>
      Array(x).fill(undefined).map((_) => random(userPool));
}
