
















import { Component } from 'vue-property-decorator';
import { Route } from 'vue-router';
import { CorePackage, ICategory, Tree, CategoryQuery, DataBus } from '@findit/core';
import globalStore, { storeCategoryTreeLens } from '@/store';
import { VueRouterExt } from '@/router/vue-router-ext';
import LogoHeader from '@/components/ui/app/LogoHeader.vue';
import ObjectDebug from '@/components/debug/ObjectDebug.vue';
import UIList from '@/components/ui/list/UIList.vue';
import UISearchBox from '@/components/ui/search-box/UISearchBox.vue';
import searchCategoriesByName = CategoryQuery.searchCategoriesByName;
import Package = CorePackage.Package;
import lift = CorePackage.lift;
import { View } from '@/components/ui/app/view';
import childrenG = Tree.childrenG;
import nodeG = Tree.nodeG;
import descendantsG = Tree.descendantsG;

export const jobRequestViewResolver = (to: Route, from: Route, next: any): void => {
  DataBus.MutableLenses.categoryTree.setM(
    to.meta,
    storeCategoryTreeLens.get(globalStore));
  next();
};

@Component({
  components: {
    UISearchBox,
    ObjectDebug,
    LogoHeader,
    UIList,
  },
  beforeRouteEnter: jobRequestViewResolver,
  beforeRouteUpdate: jobRequestViewResolver,
})
export default class JobRequestView extends View<ICategory[]> {
  protected search: string = '';

  get categories(): Package<Tree<ICategory>> {
    return DataBus.GetterLenses.categoryTree.get(this.dataBus) as Package<Tree<ICategory>>;
  }

  get model(): ICategory[] {
    const _this = this;
    return this.categories.flatMap<ICategory[]>(categories => {
      debugger;
        return !!_this.search
          ? searchCategoriesByName(lift(_this.search), false)(lift(categories))
          : lift(categories)
            .map(descendantsG<ICategory>().get)
            .map(cTrees => cTrees.map(nodeG<ICategory>().get))
      }).tap(_ => { debugger; })
      .consume();
  }

  get isEmpty(): boolean {
    return this.model.length < 1;
  }

  protected onSearch(s: string): void {
    this.search = s;
  }
}

export const JobRequestRoute = VueRouterExt.route({
  name: 'job-request',
  component: JobRequestView,
});

