// @ts-ignore
import * as load from 'load-script';

export namespace CoreHtml {

  // export const scriptLoaded
  //   : (url: string) => boolean
  //   =

  export const loadScript
    : (url: string) => Promise<HTMLScriptElement>
    = url => new Promise<HTMLScriptElement>((resolve, reject) => {
      load(url, (err: any, script: HTMLScriptElement) => {
        if (err) reject(err);
        else resolve(script);
      })
    });
}
