import * as faker from 'faker';
import { CoreRandom } from '../../src/utils/random';
import { CorePackage } from '../../src/tools/package';
import { CoreString } from '../../src/utils/string';
import { CategoryId, ICategory } from '../../src/dto/category.dto';
import { CoreUuid } from '../../src/utils/uuid';

export module MockCategory {

  import randomBetween = CoreRandom.randomBetween;
  import toTagName = CoreString.toTagName;
  import uuid = CoreUuid.uuid;

  export function random(): ICategory {
    return CorePackage.lift(`CATEGORY (${faker.random.words(randomBetween(1, 3))}`)
      .map(name => ({
        id: uuid<CategoryId>(),
        name: {
          default: name.toLowerCase(),
          full: faker.random.words(randomBetween(3, 10)),
          slug: toTagName(name)
        },
        licensed: faker.random.boolean(),
      }))
      .consume();
  }

  export function randomList(x: number):  ICategory[] {
    return CorePackage.lift(x)
      .map(x => x > 0 ? Array(x).fill(undefined) : [])
      .map(list => list.map(_ => random()))
      .consume();
  }
}
