import { BusinessObject, DefaultName, Uuid } from './business-object.dto';
import { CoreEnum } from '../utils/enum';
import typed = CoreEnum.typed;

export const Genders = typed('m', 'f', 'o');
export type Gender = keyof typeof Genders;

export type PersonId = Uuid<'Person'>;

export type PublicPersonName = {
    username: string;
}

export type PrivatePersonName = PublicPersonName & {
    first: string;
    last: string;
    nick?: string;
}

export type PublicPersonDto<Name = PublicPersonName> = BusinessObject<PersonId, Name & DefaultName>;

export type PrivatePersonDto = PublicPersonDto<PrivatePersonName> & {
    email: string;
    gender: Gender;
}
