import { CorePackage } from '../tools/package';
import { CoreLens } from '../tools/lens';
import { CoreEnum } from '../utils/enum';
import { PersonId } from '../dto/person.dto';
import { IUser } from '../dto/user.dto';
import { CategoryId, ICategory } from '../dto/category.dto';
import { Tree } from '../tools/tree';
import { ServiceId, IService } from '../dto/service.dto';
import { ListingId, IListing } from '../dto/listing.dto';
import { CoreTypes } from '../utils/typelevel';

export namespace DataBus {
  import PackageDefinition = CorePackage.PackageDefinition;
  import MutableSetterLens = CoreLens.MutableSetterLens;
  import SetterLens = CoreLens.SetterLens;
  import GetterLens = CoreLens.GetterLens;
  import propM = CoreLens.propM;
  import typed = CoreEnum.typed;
  import liftGroup = CorePackage.liftGroup;
  import lift = CorePackage.lift;
  import Maybe = CoreTypes.Maybe;
  import Nullable = CoreTypes.Nullable;
  import Package = CorePackage.Package;
  import nothing = CoreTypes.nothing;

  export const PropNames = typed(
    'userId',
    'user',

    'categoryId',
    'category',
    'categoryTree',
    'categories',
    'categorySlug',

    'serviceId',
    'services',
    'service',

    'listingId',
    'listing',
    'listings',
  );
  export type RoutePropNames = typeof PropNames;

  export type Raw = {
    userId: Maybe<PersonId>;
    user: Maybe<IUser>;
    categoryId: Maybe<CategoryId>;
    category: Maybe<ICategory>;
    categoryTree: Maybe<Tree<ICategory>>;
    categories: Maybe<ICategory[]>;
    categorySlug: Maybe<string>;
    serviceId: Maybe<ServiceId>;
    service: Maybe<IService>;
    services: Maybe<IService[]>;
    listingId: Maybe<ListingId>;
    listing: Maybe<IListing>;
    listings: Maybe<IListing[]>;
  };


  export type Packaged = PackageDefinition<Raw>;
  export type Keys = keyof Raw | keyof Packaged;

  export const MutableLenses: { [Key in Keys]: MutableSetterLens<Packaged, Packaged[Key]> } = {
    categories: propM(PropNames.categories),
    category: propM(PropNames.category),
    categoryTree: propM(PropNames.categoryTree),
    categoryId: propM(PropNames.categoryId),
    categorySlug: propM(PropNames.categorySlug),
    serviceId: propM(PropNames.serviceId),
    services: propM(PropNames.services),
    service: propM(PropNames.service),
    listings: propM(PropNames.listings),
    listing: propM(PropNames.listing),
    listingId: propM(PropNames.listingId),
    userId: propM(PropNames.userId),
    user: propM(PropNames.user),
  }
  export const SetterLenses: { [Key in Keys]: SetterLens<Packaged, Packaged[Key]> } = MutableLenses;
  export const GetterLenses: { [Key in Keys]: GetterLens<Packaged, Packaged[Key]> } = MutableLenses;


  export const toPackagedDataBus
    : <T extends DataBus.Raw>(o: T) => DataBus.Packaged
    = (o) => lift(o).map(o => liftGroup(o)).consume();


}
