import * as faker from 'faker';
import { CoreRandom } from '../../src/utils/random';
import { CoreFunction } from '../../src/utils/function';
import { CategoryDto } from '../../src/dto/category.dto';
import { OfferDto } from '../../src/dto/offer.dto';
import { PublicUserDto } from '../../src/dto/user.dto';
import { CoreUuid } from '../../src/utils/uuid';

export namespace MockOffer {
  import pipe = CoreFunction.pipe;
  import randomBetween = CoreRandom.randomBetween;

  export const random
    : (c: CategoryDto) => (u: PublicUserDto) => OfferDto
    = c => u => ({
    id: CoreUuid.uuid(),
    name: {
      default: faker.random.words(randomBetween(1, 15)),
      full: faker.random.words(randomBetween(1, 15))
    },
    category: c,
    createdBy: u,
    createdOn: faker.date.recent(),
    expiresOn: faker.date.future(),
    description: faker.random.words(randomBetween(1, 100))
  } as OfferDto);

  export const randomList
    : (c: CategoryDto) => (u: PublicUserDto) => (x: number) => Array<OfferDto>
    = c => u => pipe(
    (x: number) => Array(x).fill(undefined),
    (list: Array<undefined>) => list.map(_ => random(c)(u))
  );
}
