import { CoreFunction } from './function';

export namespace CoreString {
  import pipe = CoreFunction.pipe;

  export type Empty = '';

  export const toTagName: (s: string) => string
    = pipe(
      (s: string) => s.toLowerCase(),
      (s: string) => s.replace(/(\W)/, '-'),
      (s: string) => s.replace(/(\s)+/, ''), // remove whitespace
      (s: string) => s.replace(/(\W)+/, '-') // remove non-words
    );
}


