import { CoreTypes } from './typelevel';

export namespace CoreTuple {
  import Tuple2 = CoreTypes.Tuple2;
  import Tuple3 = CoreTypes.Tuple3;
  import Tuple4 = CoreTypes.Tuple4;
  import Tuple5 = CoreTypes.Tuple5;
  import Tuple6 = CoreTypes.Tuple6;
  import Tuple7 = CoreTypes.Tuple7;

  export const toIndexedTupleList
    : <T>(s?: number) => (ls: T[]) => Tuple2<number, T>[]
    = (s = 0) => ls => ls.map(i => [s++, i]);

  export const zip2
    : <A, B>(a: A, b: B) => Tuple2<A, B>
    = (a, b) => [a, b];

  export const zip3
    : <A, B, C>(a: A, b: B, c: C) => Tuple3<A, B, C>
    = (a, b, c) => [a, b, c];

  export const zip4
    : <A, B, C, D>(a: A, b: B, c: C, d: D) => Tuple4<A, B, C, D>
    = (a, b, c, d) => [a, b, c, d];

  export const zip5
    : <A, B, C, D, E>(a: A, b: B, c: C, d: D, e: E) => Tuple5<A, B, C, D, E>
    = (a, b, c, d, e) => [a, b, c, d, e];

  export const zip6
    : <A, B, C, D, E, F>(a: A, b: B, c: C, d: D, e: E, f: F) => Tuple6<A, B, C, D, E, F>
    = (a, b, c, d, e, f) => [a, b, c, d, e, f];

  export const zip7
    : <A, B, C, D, E, F, G>(a: A, b: B, c: C, d: D, e: E, f: F, g: G) => Tuple7<A, B, C, D, E, F, G>
    = (a, b, c, d, e, f, g) => [a, b, c, d, e, f, g];
}
