
















import { Component } from 'vue-property-decorator';
import { Route } from 'vue-router';
import { validators } from 'vue-form-generator';
import { None } from 'space-lift/es';
import {
  CategoryQuery,
  CoreForm,
  CoreHtml,
  CorePackage,
  CoreTypes,
  CoreUuid,
  CREATE_JOB_REQUEST,
  CreateJobRequestForm, DataBus,
  ICategory,
  IListing,
  PriceBracket, Tree
} from '@findit/core';
import globalStore, { storeCategoryTreeLens, storeListingsLens } from '@/store';
import { VueRouterExt } from '@/router/vue-router-ext';
import { View } from '@/components/ui/app/view';
import UIText from '@/components/ui/text/UIText.vue';
import LogoHeader from '@/components/ui/app/LogoHeader.vue';
import UIForm from '@/components/ui/form/UIForm.vue';
import ObjectDebug from '@/components/debug/ObjectDebug.vue';
import FieldSchema = CoreForm.FieldSchema;
import FieldTypes = CoreForm.FieldTypes;
import Options = CoreForm.Options;
import FieldError = CoreForm.FieldError;
import Field = CoreForm.Field;
import nothing = CoreTypes.nothing;
import Package = CorePackage.Package;
import lift = CorePackage.lift;
import toRawDataBus = VueRouterExt.toRawDataBus;
import toPackagedDataBus = DataBus.toPackagedDataBus;
import searchCategoriesBySlug = CategoryQuery.searchCategoriesBySlug;
import treeLeafNode = Tree.treeLeafNode;
import nodeG = Tree.nodeG;

const createJobResolver
      : (to: Route, from: Route, next: any) => void
      = (to, from, next) => {
  const categorySlug = lift(DataBus.GetterLenses.categorySlug.get(
    toPackagedDataBus(toRawDataBus(to.params))
  ));

  DataBus.MutableLenses.categoryTree.setM(to.meta,
    searchCategoriesBySlug(categorySlug.orElse(() => ''), false)(storeCategoryTreeLens.get(globalStore),
  ))

  DataBus.MutableLenses.listings.setM(to.meta, storeListingsLens.get(globalStore));

  if (!(window as any).google?.maps) {
    CoreHtml.loadScript('https://maps.googleapis.com/maps/api/js?key=AIzaSyCwMGbBhnEkYWcPQ5XYU-kJMv1pehBWWEY&libraries=places').then(_ => {
      next();
    });
  } else {
    next();
  }
};

@Component({
  components: {ObjectDebug, UIForm, LogoHeader, UIText},
  beforeRouteEnter: createJobResolver,
  beforeRouteUpdate: createJobResolver,
})
export default class CreateJobRequestView extends View<ICategory> {
  private emptyModel: ICategory = {
    id: CoreUuid.empty(),
    licensed: false,
    name: {
      default: '<NOT_FOUND>',
      full: 'Not Found',
      slug: '404'
    },
  };

  protected get category(): Package<ICategory> {
    return DataBus.GetterLenses.categoryTree
      .get(this.dataBus)
      .orElse(() => treeLeafNode<ICategory>()(() => this.emptyModel))
      .map(nodeG<ICategory>().get)
  }

  protected get listings(): Package<IListing[]> {
    return DataBus.GetterLenses.listings
      .get(this.dataBus)
      .orElse(() => [] as IListing[]);
  }

  protected get hasCategory(): boolean {
    return this.category.isDefined() && !!this.category.consume();
  }

  protected get model(): ICategory {
    return this.category.consume();
  }

  protected get formModel(): CreateJobRequestForm {
    if (!this.hasCategory) {
      this.$router.go(-1);
      throw new Error('CreateJob: service was not defined');
    }
    return {
      category: this.model.id,
      location: '',
      los: PriceBracket.MID, // TODO Define default price bracket
      asap: true,
      dateTime: new Date(),
      notes: None,
      premises: None,
      meta: nothing,
    };
  }

  protected get formSchema(): FieldSchema<CreateJobRequestForm> {
    if (!this.hasCategory) {
      throw new Error('CreateJob: service was not defined');
    }
    return {
      groups: [
        {
          legend: 'Request a Job',
          fields: [
            {
              type: FieldTypes.LABEL,
              model: 'category',
              label: this.model.name.default,
            },
            {
              type: FieldTypes.INPUT,
              model: 'location',
              label: 'Location',
              inputType: 'text',
              required: true,
              validator: validators.string,
            },
            {
              type: FieldTypes.RADIOS,
              model: 'los',
              label: 'Level of Service',
              values: [PriceBracket.LOW, PriceBracket.MID, PriceBracket.HI],
              required: true,
            },
            {
              type: FieldTypes.RADIOS,
              model: 'asap',
              label: 'Time',
              required: true,
              default: true,
              values: [{
                name: 'As soon as Possible',
                value: true,
              }, {
                name: 'Future Booking',
                value: false,
              }],
            },
            {
              type: FieldTypes.INPUT,
              model: 'dateTime',
              label: 'When?',
              inputType: 'datetime',
              required: true,
              visible: (model, schema) => !model.asap,
              validator: validators.date,
            },
            {
              type: FieldTypes.TEXTAREA,
              model: 'notes',
              label: 'Message (optional)'
            },
            {
              type: FieldTypes.SUBMIT,
              label: 'Create',
              validateBeforeSubmit: true,

              onSubmit: () => {
                console.debug(this.formModel)
                globalStore.dispatch(CREATE_JOB_REQUEST, {
                  model: this.formModel,
                });
              }
            }
          ]
        },
      ]
    }
  }

  protected get formOptions(): Options {
    return {
      validateAfterChanged: true,
    };
  }

  protected onValidated(
    isValid: boolean,
    errors: FieldError<CreateJobRequestForm, Field<CreateJobRequestForm>>[]
  ) {
    errors.forEach(e => console.debug(e.field.model, e.error));
    console.debug('onValidated', isValid, errors);
  }

  protected onChanged<Key extends keyof CreateJobRequestForm>(
    model: CreateJobRequestForm[Key],
    schema: Key
  ) {
    console.debug('onChanged', model, schema);
  }
}

export const CreateJobRequestRoute = VueRouterExt.route({
  name: 'create-job-request',
  component: CreateJobRequestView,
  path: 'create-job/category/:categorySlug',
  meta: {
    back: true
  }
});
