
















import { Component } from 'vue-property-decorator';
import { Route } from 'vue-router';
import { CategoryQuery, CorePackage, CoreTypes, DataBus, ICategory, Tree } from '@findit/core';
import globalStore, { storeCategoryTreeLens } from '@/store';
import { VueRouterExt } from '@/router/vue-router-ext';
import LogoHeader from '@/components/ui/app/LogoHeader.vue';
import ObjectDebug from '@/components/debug/ObjectDebug.vue';
import CategoryTree from '@/components/category/CategoryTree.vue';
import Package = CorePackage.Package;
import searchCategoriesBySlug = CategoryQuery.searchCategoriesBySlug;
import toRawDataBus = VueRouterExt.toRawDataBus;
import toPackagedDataBus = DataBus.toPackagedDataBus;
import Nothing = CoreTypes.Nothing;
import nothing = CoreTypes.nothing;
import { View } from '@/components/ui/app/view';

export const categoryResolver = (to: Route, from: Route, next: any): void => {
  DataBus.MutableLenses.categoryTree.setM(to.meta,
    searchCategoriesBySlug(
      DataBus.GetterLenses.categorySlug.get(toPackagedDataBus(toRawDataBus(to.params))) as Package<string>,
      false,
    )(storeCategoryTreeLens.get(globalStore))
  );
  next();
};

@Component({
  components: {
    LogoHeader,
    ObjectDebug,
    CategoryTree,
  },
  beforeRouteEnter: categoryResolver,
  beforeRouteUpdate: categoryResolver,
})
export default class CategoryView extends View<Package<Tree<ICategory>>> {
  protected get model(): Package<Tree<ICategory>> {
    return DataBus.GetterLenses.categoryTree.get(this.dataBus) as Package<Tree<ICategory>>;
  }

  protected get exists(): boolean {
    return this.model.isDefined();
  }

  protected get categoryTree(): Tree<ICategory> {
    return this.model.consume();
  }

  protected get category(): ICategory | Nothing {
    return this.model.map(t => t ? t.node.get() : nothing).consume();
  }

  protected get hasChildren(): boolean {
    return this.model
      .map(t => t.children)
      .map(cs => cs.map(_cs => _cs.count() > 0))
      .map(cs => cs.getOrElse(false))
      .consume();
  }
}

export const CategoryRoute = VueRouterExt.route({
  name: 'category',
  path: 'category/:categorySlug',
  component: CategoryView,
  meta: {
    back: true
  }
});

