











import { Vue, Component, Prop } from 'vue-property-decorator';
import { ICategory, Tree } from '@findit/core';
import CategoryListItem from '@/components/category/CategoryListItem.vue';
import UIList from '@/components/ui/list/UIList.vue';

@Component({
  components: {
    UIList,
    CategoryListItem
  }
})
export default class CategoryTree extends Vue {
  @Prop() protected model!: Tree<ICategory>;

  get items(): ICategory[] {
    return this.model.children
      .map(cs => cs.map(c => c.node.get() || {} as ICategory))
      .map(cs => cs.toArray())
      .getOrElse([] as ICategory[]);
  }
}
