import { CoreTypes } from '../utils/typelevel';

export namespace CoreForm {
  import Predicate = CoreTypes.Predicate;

  export enum FieldTypes {
    CHECKBOX = 'checkbox', // Checkbox for boolean values
    CHECKLIST = 'checklist', // Checkbox list to select multiple values
    INPUT = 'input', // Common input field NEW!
    LABEL = 'label', // Static text (e.g. timestamp, creation time...etc)
    RADIOS = 'radios', // Radio buttons to select NEW!
    SELECT = 'select', // Select list
    SUBMIT = 'submit', // This is a simple submit button
    TEXTAREA = 'textArea', // Text area field
    GOOGLE_ADDRESS = 'googleAddress', // This is an input field that auto-complete address with Google Address.
  }

  export type FieldType = FieldTypes.CHECKBOX
    | FieldTypes.CHECKLIST
    | FieldTypes.INPUT
    | FieldTypes.LABEL
    | FieldTypes.RADIOS
    | FieldTypes.SELECT
    | FieldTypes.SUBMIT
    | FieldTypes.TEXTAREA
    | FieldTypes.GOOGLE_ADDRESS;

  export type FieldValidator<Form> =
    string // name of the validator
    | (<T extends Form[keyof Form]>(value: string, field: BuiltInField<Form>, model: T) => string[]);

  export type FieldValue = string | boolean | number;
  export type FieldPredicate<Form, FieldSchema extends Field<Form>> = (model: Form, schema: FieldSchema) => boolean;
  export type FieldError<Form, FieldSchema extends Field<Form>> = {
    error: string;
    field: FieldSchema;
  }

  export type SelectFieldValue = string | {
    id: string;
    name: string;
  };

  export type RadiosFieldValue = FieldValue | {
    /**
     * Default: none, The text displayed beside the
     * radio button (ie; Label)
     */
    name?: string;

    /**
     * Default: none, The value of the radio option
     * stored in the model
     */
    value?: FieldValue;

    /**
     * Default: none, Used to disable this radio option
     */
    disabled?: boolean | Predicate<void>;
  };

  export type Button<Form, Field> = {
    /**
     * Default: none, CSS classes for the button
     */
    classes?: string;

    /**
     * Default: none, Caption of the button
     */
    label?: string;

    /**
     * Default: none, Triggered when click on the button
     */
    onclick?: (model: Form, field: Field) => void;

    /**
     * Default: 'button', The HTML "type" attribute for the
     * button, defaults to "button"
     */
    type?: string;
  };

  export type Field<Form> = {
    /**
     * Type of field
     */
    type: FieldType;

    /**
     * Label of field
     */
    label?: string;

    /**
     * Name of property in the model
     */
    model?: keyof Form;

    /**
     * id of the field. If not set, will be auto-generated
     * from the slugified version of either: schema.inputName,
     * schema.label or schema.model, in that order. If the
     * fieldIdPrefix option is set, it's value will be prepended
     * to both manual & auto-generated ids.
     */
    id?: string;

    /**
     * is it a featured (bold) field? Can be a function too.
     */
    featured?: boolean | FieldPredicate<Form, Field<Form>>;

    /**
     * if false, field will be hidden. Can be a function too.
     */
    visible?: boolean | FieldPredicate<Form, Field<Form>>;

    /**
     * if true, field will be disabled. Can be a function too.
     */
    disabled?: boolean | FieldPredicate<Form, Field<Form>>;

    /**
     * If true, Stylizes the field as required.  Works in
     * conjunction with validators.
     */
    required?: boolean;

    /**
     * Default value of the field (used when creating
     * a new model)
     */
    default?: boolean;

    /**
     * Show this hint below the field
     */
    hint?: string;

    /**
     * Tooltip/Popover triggered by hovering over the
     * question icon before the caption of field. You
     * can use HTML elements
     * too.
     */
    help?: string;

    /**
     * Validator for value. It can be an array of functions too.
     */
    validator?: FieldValidator<Form> | FieldValidator<Form>[];

    /**
     * Amount of time in milliseconds validation waits
     * before checking, refer to validation
     */
    validateDebounceTime?: number;

    /**
     * Custom CSS style classes. They will be appended
     * to the `.from-group`
     */
    styleClasses?: string | string[];

    /**
     * Array of button objects. This is useful if you need
     * some helper function to fill the field. (E.g.
     * generate password, get GPS location..etc)*
     */
    buttons?: Button<Form, Field<Form>>[];

    /**
     * You can add custom HTML Attributes, such as
     * data-attributes, to fields by using an attributes
     * object.  You can also specify the attributes of
     * the surrounding wrapper and label.
     */
    attributes?: Object;

    /**
     * A read formatter function where you can format
     * the model value to the field
     */
    get?: <T extends Form[keyof Form]>(model: keyof Form) => T;

    /**
     * A write formatter function where you can format
     * the field value to the model
     */
    set?: <T extends Form[keyof Form]>(model: keyof Form, value: T) => void;

    /**
     * Triggered if the value of field is changed.
     */
    onChanged?: <T extends Form[keyof Form]>(model: keyof Form, newVal: T, oldVal: T, field: HTMLElement) => void;

    /**
     * Triggered if validation of field is executed.
     */
    onValidated?: (model: keyof Form, errors: Object[], field: HTMLElement) => void;

  };

  export type InputField<Form> = Field<Form> & {
    type: FieldTypes.INPUT;

    /**
     * Default: none, set name attribute to input field.
     * You can use it to generate normal HTML Forms and
     * submit the field values to server-side.
     */
    inputType: string;

    /**
     * Default: none, Placeholder text for input field
     */
    placeholder?: string;

    /**
     * Default: false, If true, the input field is read only
     */
    readonly?: boolean;
  };

  export type PasswordField<Form> = InputField<Form> & {
    inputType: 'password';
    min: number;
  };

  export type SelectField<Form> = Field<Form> & {
    type: FieldTypes.SELECT;

    /**
     * Default: none, List of items. It can be an array
     * with items, or a Function, what is resulted an
     * array. The item will be a String or an object with
     * an id and a name properties.
     */
    values: SelectFieldValue[];

    /**
     * if true, it will be visible only  if multiple is
     * true in component attributes
     */
    multi?: boolean;

    /**
     * Default: {}, Settings to select component. See
     * details below.
     */
    selectOptions?: {

      /**
       * Default: '<Nothing selected>', Change the text for
       * the no selection option.
       */
      noneSelectedText?: string;

      /**
       * Default: false, Hide the noneSelected item
       */
      hideNoneSelectedText?: boolean;

      /**
       * Default: 'id', Used to select any properties from
       * object in values to use as actual value to save
       * in model.
       */
      value?: string;

      /**
       * Default: 'name', Used to select any properties from
       * object in name to use as display in the list
       */
      name?: string;
    }
  };

  export type CheckboxField<Form> = Field<Form> & {
    type: FieldTypes.CHECKBOX;

    /**
     * Default: none, Indicates whether the value of
     * the control can be automatically completed by
     * the browser.
     */
    autocomplete?: string;
  };

  export type ChecklistField<Form> = Field<Form> & {
    type: FieldTypes.CHECKLIST;

    /**
     * Default: false, If true, render the items as a listBox.
     * If false, render as a comboBox.
     */
    listBox?: boolean;

    /**
     * Default: none, List of items. It can be an array
     * with items, or a Function, what is resulted an
     * array. The item can be a String, Boolean, Number
     * or an Object (with an value and a name properties).
     * You can change value and name (under checklistOptions)
     * to select any properties of that object as value
     * or name.
     */
    values: FieldValue[] | (() => FieldValue[]);

    /**
     * Default: {}, Settings to checklist component. See
     * details below.
     */
    checklistOptions?: {
      /**
       * Default: none, Used to select any properties from object in
       * values to use as actual value to save in model.
       */
      value?: string;

      /**
       * Default: none, Used to select any properties from object in
       * name to use as display in the list
       */
      name?: string;
    };
  };

  export type LabelField<Form> = Field<Form> & {
    type: FieldTypes.LABEL;
    // There are no special properties.
  };

  export type RadiosField<Form> = Field<Form> & {
    type: FieldTypes.RADIOS;

    /**
     * Default: none, List of items.
     */
    values: RadiosFieldValue[] | (() => RadiosFieldValue[]);

    /**
     * Default: {}, Settings to checklist component. See
     * details below.
     */
    radiosOptions?: {
      /**
       * Default: none, Used to select any properties from object in
       * values to use as actual value to save in model.
       */
      value?: FieldValue;

      /**
       * Default: none, Used to select any properties from object in
       * name to use as display in the list
       */
      name?: string;
    };
  }

  export type SubmitField<Form> = Field<Form> & {
    type: FieldTypes.SUBMIT;

    /**
     * Default: none, text of submit button
     */
    buttonText?: string;

    /**
     * Default: none, Will be called when you press the
     * button
     */
    onSubmit?: (form: Form, field: SubmitField<Form>, event: Event) => void;

    /**
     * Default: false, If true, run validation after the
     * button click. If validation is successful, call the
     * onSubmit callback from the schema
     */
    validateBeforeSubmit?: boolean;
  }

  export type TextAreaField<Form> = Field<Form> & {
    type: FieldTypes.TEXTAREA;

    /**
     * Default: none, Indicates whether the value of the
     * control can be automatically completed by the browser.
     */
    autocomplete?: string;

    /**
     * Default: none, maxlength value (need to use
     * validators.number)
     */
    max?: number;

    /**
     * Default: none, minlength value (need to use
     * validators.number)
     */
    min?: number;

    /**
     * Default: 2, Number of rows.
     */
    rows?: number;

    /**
     * Default: none, Placeholder text for input field
     */
    placeholder?: string;

    /**
     * Default: false, If true, the input field is read only
     */
    readonly?: boolean;
  }

  export type GoogleAddressField<Form> = Field<Form> & {
    type: FieldTypes.GOOGLE_ADDRESS;

    /**
     * Default: none, Indicates whether the value of the
     * control can be automatically completed by the browser.
     */
    autocomplete?: string;

    /**
     * Default: none, Will be called when the user select an
     * address from list.
     * @param value
     * @param place
     * @param rawPlace
     * @param model
     * @param schema
     */
    onPlaceChanged?: (value: any, place: any, rawPlace: any, model: any, schema: any) => void;

    /**
     * Default: none, Placeholder text for input field
     */
    placeholder?: string;

    /**
     * Default: false, If true, the input field is read only
     */
    readonly?: boolean;
  }

  export type BuiltInField<Form> = InputField<Form>
    | PasswordField<Form>
    | CheckboxField<Form>
    | ChecklistField<Form>
    | LabelField<Form>
    | RadiosField<Form>
    | SelectField<Form>
    | SubmitField<Form>
    | TextAreaField<Form>
    | GoogleAddressField<Form>;

  export type FieldGroup<F> = {
    legend?: string;
    fields: BuiltInField<F>[];
  };

  export type FieldSchema<F> = {
    groups: FieldGroup<F>[];
  };

  export type Options = {
    /**
     * Default: false, Execute validation after model loaded
     */
    validateAfterLoad?: boolean;

    /**
     * Default: false, Execute validation after every change
     */
    validateAfterChanged?: boolean;

    /**
     * Default: none, Prefix to add to every field's id.
     * Will be prepended to ids explicitly set in the schema,
     * as well as auto-generated ones.
     */
    fieldIdPrefix?: string;

    /**
     * Default: false, validate() method returns a Promise,
     * refer to validation
     */
    validateAsync?: boolean;

    /**
     * Default: 'error', CSS Class attached to elements which
     * are invalid
     */
    validationErrorClass?: string;

    /**
     * Default: none, CSS Class attached to elements which
     * are valid
     */
    validationSuccessClass?: string;

    /**
     * Default: 0, Amount of time in milliseconds validation
     * waits before checking, refer to validation
     */
    validateDebounceTime?: number;
  };
}
