import { CorePackage } from '../tools/package';
import { IPrivateUser, IPublicUser } from '../dto/user.dto';
import { ITopic } from '../dto/topic.dto';
import { IListing } from '../dto/listing.dto';
import { CategoryId, ICategory } from '../dto/category.dto';
import { Tree } from '../tools/tree';
import { IService } from '../dto/service.dto';
import { CoreLens } from '../tools/lens';
import Package = CorePackage.Package;


export interface IStore {
  user: Package<IPrivateUser>;
  users: Package<IPublicUser[]>;
  topics: Package<ITopic[]>;
  listings: Package<IListing[]>;
  categories: Package<ICategory[]>;
  categoryIdTree: Package<Tree<CategoryId>>;
  services: Package<IService[]>;
}

export namespace IStore {
  import propG = CoreLens.propG;
  import and2G = CoreLens.and2G;
  import composeG = CoreLens.composeG;
  import prism = CoreLens.prism;
  export const userLens = propG<IStore, 'user'>('user');
  export const usersLens = propG<IStore, 'users'>('users');
  export const topicsLens = propG<IStore, 'topics'>('topics');
  export const listingsLens = propG<IStore, 'listings'>('listings');
  export const categoriesLens = propG<IStore, 'categories'>('categories');
  export const categoryIdTreeLens = propG<IStore, 'categoryIdTree'>('categoryIdTree');
  export const categoriesAndCategoryIdTreeLens = and2G(
    categoriesLens,
    categoryIdTreeLens,
  );
  export const categoryTreeLens = composeG(
    categoriesAndCategoryIdTreeLens,
    prism(([categoriesP, treeP]) => categoriesP.zip(treeP).map(([categories, tree]) =>
      tree.map(t => categories.find(c => c.id === t) as ICategory)
    ))
  )
  export const servicesLens = propG<IStore, 'services'>('services');

}
