import { Vue } from 'vue-property-decorator';
import { CorePackage, CoreTypes, DataBus } from '@findit/core';
import toPackagedDataBus = DataBus.toPackagedDataBus;
import Nothing = CoreTypes.Nothing;
import Package = CorePackage.Package;

export abstract class View<Model = Nothing> extends Vue {
  protected abstract get model(): Model;

  protected get dataBus(): DataBus.Packaged {
    return toPackagedDataBus(this.$route.meta);
  }
}
