



























import { Vue, Component } from 'vue-property-decorator';
import { RouteConfig } from 'vue-router';
import VueFormGenerator from 'vue-form-generator';
import { CoreForm, CoreString, CoreTuple } from '@findit/core';
import LogoHeader from '@/components/ui/app/LogoHeader.vue';
import UIButton from '@/components/ui/button/UIButton.vue';
import { Button } from '@/components/ui/button/button';
import UISearchBox from '@/components/ui/search-box/UISearchBox.vue';
import UIText from '@/components/ui/text/UIText.vue';
import UITextField from '@/components/ui/text-field/UITextField.vue';
import toIndexedTupleList = CoreTuple.toIndexedTupleList;
import UISelect from '@/components/ui/select/UISelect.vue';
import UICheckbox from '@/components/ui/checkbox/UICheckbox.vue';
import UIForm from '@/components/ui/form/UIForm.vue';
import FieldSchema = CoreForm.FieldSchema;
import FieldTypes = CoreForm.FieldTypes;
import { VueRouterExt } from '@/router/vue-router-ext';

type TestForm = {
  id: number;
  name: string;
  password: string;
  skills: CoreString.Empty | string[];
  level: CoreString.Empty | string[];
  options: string[];
  email: string;
  accept: boolean;
};

@Component({
  components: {
    UICheckbox,
    UISelect,
    UITextField,
    UIText,
    UISearchBox,
    LogoHeader,
    UIButton,
    UIForm,
  },
})
export default class KitchenSinkView extends Vue {
  protected buttonStates: Button.TState[] = Object.values(Button.State);

  protected toIndexedTupleList = toIndexedTupleList;

  protected onButtonClick(): Promise<void> {
    return new Promise((resolve, reject) => {
      // nothing

      setTimeout(() => {
        (Math.round(Math.random()) > 0) ? resolve() : reject()
      }, 5000);
    });
  }

  protected onSearch(value: string): void {
    console.log(`SearchBox-onSearch: ${value}`);
  }

  get formModel(): TestForm {
    return {
      id: 1,
      name: 'John Doe',
      password: '',
      skills: '',
      level: '',
      options: [],
      email: 'john.doe@gmail.com',
      accept: false
    };
  }

  get formSchema(): FieldSchema<TestForm> {
    return {
      groups: [
        {
          legend: 'User Details',
          fields: [
            {
              type: FieldTypes.INPUT,
              inputType: 'text',
              label: 'ID (disabled text field)',
              model: 'id',
              readonly: true,
              disabled: true,
            },
            {
              type: FieldTypes.INPUT,
              inputType: 'text',
              label: 'Name',
              model: 'name',
              id: 'user_name',
              placeholder: 'Your name',
              featured: true,
              required: true,
              validator: VueFormGenerator.validators.required,
            },
            {
              type: FieldTypes.INPUT,
              inputType: 'email',
              label: 'E-mail',
              model: 'email',
              placeholder: 'User\'s e-mail address',
              validator: VueFormGenerator.validators.email,
            },
            {
              type: FieldTypes.INPUT,
              inputType: 'password',
              label: 'Password',
              model: 'password',
              min: 6,
              required: true,
              hint: 'Minimum 6 characters',
              validator: VueFormGenerator.validators.required,
            }
          ]
        },
        {
          legend: 'Skills & Status',
          fields: [
            {
              type: FieldTypes.SELECT,
              label: 'Skills',
              model: 'skills',
              values: ['Javascript', 'VueJS', 'CSS3', 'HTML5'],
              // listBox: true,
              required: true,
              validator: VueFormGenerator.validators.required,
            },
            {
              type: FieldTypes.RADIOS,
              label: 'Level',
              model: 'level',
              values: ['Basic', 'Advanced', 'Premium'],
              // listBox: true,
              required: true,
              validator: VueFormGenerator.validators.required,
            },
            {
              type: FieldTypes.CHECKLIST,
              label: 'Options',
              model: 'options',
              values: ['SMS', 'Email', 'SMS & Email'],
              // listBox: true,
              required: true,
              validator: VueFormGenerator.validators.required,
            },
            {
              type: FieldTypes.CHECKBOX,
              label: 'Accept',
              model: 'accept',
              default: false,
              required: true,
              validator: VueFormGenerator.validators.required,
            }
          ]
        },
        {
          legend: 'All Done',
          fields: [
            {
              type: FieldTypes.SUBMIT,
              validateBeforeSubmit: true,
              onSubmit: (form, field, event) => {
                return true;
              }
            }
          ]
        }
      ]
    };
  }

  // get formOptions(): Form.Options {
  //   return {
  //     validateAfterLoad: true,
  //     validateAfterChanged: true,
  //     validateAsync: true,
  //   };
  // }
}

export const KitchenSinkRoute = VueRouterExt.route({
  name: 'kitchen-sink',
  path: '/kitchen-sink',
  component: KitchenSinkView,
  meta: {
    back: true
  }
});
