import { Option } from 'space-lift/es';
import { CoreLens } from './lens';
import { CoreTypes } from '../utils/typelevel';
import StateMonad = CoreTypes.StateMonad;
import Defer = CoreTypes.Defer;
import Maybe = CoreTypes.Maybe;
import MapFn = CoreTypes.MapFn;
import Predicate = CoreTypes.Predicate;
import Nothing = CoreTypes.Nothing;
import nothing = CoreTypes.nothing;

export namespace CorePackage {

  import propS = CoreLens.propS;
  import Tuple5 = CoreTypes.Tuple5;
  import Tuple4 = CoreTypes.Tuple4;
  import Tuple3 = CoreTypes.Tuple3;
  import Tuple2 = CoreTypes.Tuple2;
  import Tuple6 = CoreTypes.Tuple6;
  import Tuple7 = CoreTypes.Tuple7;

  export class Package<T> implements StateMonad<T> {
    constructor(
      readonly value: Defer<T>
    ) {
    }

    map<U>(fn: MapFn<T, U>): Package<U> {
      try {
        return lift(fn(this.consume()));
      } catch (e) {
        return lift(e);
      }
    }

    filter(p: Predicate<T>): Package<Maybe<T>> {
      const consumed = this.consume();
      if (consumed && p(consumed)) {
        return this;
      } else {
        return CorePackage.lift(CoreTypes.nothing);
      }
    }

    tap(fn: MapFn<T, any>): Package<T> {
      try {
        fn(this.consume());
        return this;
      } catch (e) {
        return lift(e);
      }
    }

    flatMap<U>(fn: MapFn<T, Package<U>>): Package<U> {
      try {
        return lift(fn(this.consume()).consume());
      } catch (e) {
        return lift(e);
      }
    }

    flatMapPkg<U>(fn: MapFn<Package<T>, Package<U>>): Package<U> {
      try {
        return lift(fn(this).consume());
      } catch (e) {
        return lift(e);
      }
    }

    zip<U>(u: Package<U>): Package<Tuple2<T, U>> {
      return lift([this.consume(), u.consume()]);
    }

    zipValue<U>(u: Defer<U>): Package<Tuple2<T, U>> {
      return lift([this.consume(), u()]);
    }

    toOption(): Option<T> {
      return Option(this.consume());
    }

    isDefined(): boolean {
      return this.value !== nothing && this.value() !== nothing;
    }

    orElse(this: Package<T>, otherwise: Defer<MaybeInner<T>>): Package<MaybeInner<T>> {
      return this.flatMap(v =>
        v !== nothing
          ? lift(v as MaybeInner<T>)
          : lift(otherwise())
      );
      // const x = unwrapMaybe(this.value());

      // return this/*.map(unwrapMaybe)*/.flatMap(v =>
      //   v === nothing
      //     ? lift(otherwise)
      //     : lift(v)
      // )
      // return this.map(unwrapMaybe(otherwise as UnwrappedMaybe<T>));
    }

    catch(fn: MapFn<Error, T>): Package<T> {
      return this.flatMap<T>(v =>
        (v instanceof Error)
          ? lift(fn(v))
          : lift(v)
      );
    }

    consume(): T {
      // if (this.isDefined() && this.map(v => !(v instanceof Error))) {
      //   return this.value();
      // } else if (this.isDefined()) {
      //   throw this.consume();
      // } else {
      //   debugger;
      //   throw new Error("Package: 'consume' was called on an empty package");
      // }
      return this.value();
    }
  }

  // export type RawPackageDefinition<T extends {}> = { [K in (string & keyof T)]: T[K] }
  export type PackageDefinition<T extends {}> = { [K in (string & keyof T)]: Package<T[K]> };

  function isPackage<T>(arg: any): arg is Package<T> {
    return arg instanceof Package;
  }

  // declare function unwrapMaybe<T>(otherwise: T): T extends Maybe<infer U>
  //   ? U extends undefined
  //     ? Nothing
  //     : U
  //   : T;
  export function unwrapMaybe<T>(otherwise: T): (input: Maybe<T>) => T {
    return input => input !== nothing ? input : otherwise;
  }

  // declare function MaybeType<T>(t: T): T extends Maybe<infer U> ? Package<U> : never;
  export type MaybeInner<T> = T extends Maybe<infer U> ? U extends Nothing ? never : U : T;

  export function lift<T>(t: T | Package<T>): Package<T> {
    return isPackage<T>(t)
      ? t
      : new Package<T>(() => t as T);
  }

  export function liftOption<T>(o: Option<T>): Package<Maybe<T>> {
    return lift(o.get());
  }

  export function liftGroup<T extends {}, K extends keyof PackageDefinition<T>>(group: T): PackageDefinition<T> {
    return lift(group)
      .map(object => Object.entries(object))
      .map(entries => entries.reduce<Partial<PackageDefinition<T>>>(
        (definition, [key, value]: [K, T[K]]) => {
          const lensS = propS<Partial<PackageDefinition<T>>, keyof PackageDefinition<T>>(key);
          return lensS.set(definition, lift(value as T[K]));
        },
        {}
      ))
      .map(definition => definition as PackageDefinition<T>)
      .consume();
  }

  export function flatten2<U1, U2>(
    u1: Package<U1>,
    u2: Package<U2>,
  ): Package<Tuple2<U1, U2>> {
    return lift([
      u1.consume(),
      u2.consume(),
    ]);
  }

  export function flatten3<U1, U2, U3>(
    u1: Package<U1>,
    u2: Package<U2>,
    u3: Package<U3>,
  ): Package<Tuple3<U1, U2, U3>> {
    return lift([
      u1.consume(),
      u2.consume(),
      u3.consume(),
    ]);
  }

  export function flatten4<U1, U2, U3, U4>(
    u1: Package<U1>,
    u2: Package<U2>,
    u3: Package<U3>,
    u4: Package<U4>,
  ): Package<Tuple4<U1, U2, U3, U4>> {
    return lift([
      u1.consume(),
      u2.consume(),
      u3.consume(),
      u4.consume(),
    ]);
  }

  export function flatten5<U1, U2, U3, U4, U5>(
    u1: Package<U1>,
    u2: Package<U2>,
    u3: Package<U3>,
    u4: Package<U4>,
    u5: Package<U5>,
  ): Package<Tuple5<U1, U2, U3, U4, U5>> {
    return lift([
      u1.consume(),
      u2.consume(),
      u3.consume(),
      u4.consume(),
      u5.consume(),
    ]);
  }

  export function flatten6<U1, U2, U3, U4, U5, U6>(
    u1: Package<U1>,
    u2: Package<U2>,
    u3: Package<U3>,
    u4: Package<U4>,
    u5: Package<U5>,
    u6: Package<U6>,
  ): Package<Tuple6<U1, U2, U3, U4, U5, U6>> {
    return lift([
      u1.consume(),
      u2.consume(),
      u3.consume(),
      u4.consume(),
      u5.consume(),
      u6.consume(),
    ]);
  }

  export function flatten7<U1, U2, U3, U4, U5, U6, U7>(
    u1: Package<U1>,
    u2: Package<U2>,
    u3: Package<U3>,
    u4: Package<U4>,
    u5: Package<U5>,
    u6: Package<U6>,
    u7: Package<U7>,
  ): Package<Tuple7<U1, U2, U3, U4, U5, U6, U7>> {
    return lift([
      u1.consume(),
      u2.consume(),
      u3.consume(),
      u4.consume(),
      u5.consume(),
      u6.consume(),
      u7.consume(),
    ]);
  }


  export const empty: Package<Nothing> = lift(nothing);
}
