import { v4 as uuidV4 } from 'uuid';
import { Uuid } from '../dto/business-object.dto';

export namespace CoreUuid {
  const toUuid
    = <T extends Uuid<string>>(u: string): T => u as T;

  export const uuid
    : <T extends Uuid<string> = Uuid<'Uuid'>>() => T
    = () => toUuid(uuidV4());

  export const empty
    : <T extends Uuid<string> = Uuid<'Uuid'>>() => T
    = () => toUuid('00000000-0000-0000-0000-000000000000');

}
