import { Option, Some } from 'space-lift/es';
import { CoreFunction } from './function';

export namespace CoreRandom {
  import pipe = CoreFunction.pipe;

  export const randomBetween: (l: number, u: number, excl?: Option<number>) => number
    = (l, u) => Math.floor(Math.random() * (u - l)) + l;

  export const randomFrom: <T>(ls: Array<T>, excl: Option<T[]>) => T
    = ls => ls[randomBetween(0, ls.length - 1)];

  export const xRandomFrom: <T>(ls: Array<T>, excl: Option<Array<T>>) => (x: number) => T[]
    = (ls, excl) =>
    pipe(
      (x: number) =>
        (x > 0)
          ? Some(xRandomFrom(ls, excl)(x - 1))
            .map(rest => rest.concat(randomFrom(ls, excl.map(l => l.concat(rest)))))
            .getOrElse([])
          : []
    );
}
