











import Vue from 'vue';
import { Component, Prop } from 'vue-property-decorator';
import UIText from '@/components/ui/text/UIText.vue';
import { CoreTypes, CoreUuid } from '@findit/core';
import Tuple2 = CoreTypes.Tuple2;
import uuid = CoreUuid.uuid;

@Component({
  components: {UIText}
})
export default class UISelect extends Vue {
  @Prop({ default: `ui-search-box-${uuid()}` }) protected id!: string;
  @Prop({ default: '' }) protected label!: string;
  @Prop({ default: [] }) protected values!: Tuple2<number, string>[];
  @Prop({ default: 0 }) protected preset!: number;
}
