






























import { Vue, Component } from 'vue-property-decorator';
import { CorePackage, IPrivateUser, IService, IServiceProviderUser, UserQuery } from '@findit/core';
import globalStore, { storeServicesLens, storeUserLens } from '@/store';
import Package = CorePackage.Package;
import userToServiceProvider = UserQuery.userToServiceProvider;

@Component({})
export default class LogoHeader extends Vue {
  get user(): Package<IPrivateUser & IServiceProviderUser> {
    return storeUserLens.get(globalStore).map(userToServiceProvider(this.services.consume()));
  }

  get services(): Package<IService[]> {
    return storeServicesLens.get(globalStore);
  }

  get model(): IPrivateUser & IServiceProviderUser {
    return this.user.consume();
  }

  get showBack(): boolean {
    return !!this.$route.meta.back;
  }

  get showNext(): boolean {
    return !!this.$route.meta.next;
  }

  get showLogo(): boolean {
    return true;
  }
}
