










import { Vue, Component, Prop } from 'vue-property-decorator';

@Component({})
export default class UIList extends Vue {
  @Prop({ required: true }) protected model!: any[];
  @Prop({ required: false, default: 1 }) protected columns!: number;

  get items(): any[] {
    return this.model;
  }

  get columnClassModifier(): string {
    return `${Math.min(this.model.length, this.columns)}column`;
  }
}
