














import { Component } from 'vue-property-decorator';
import UISearchBox from '@/components/ui/search-box/UISearchBox.vue';
import { Route } from 'vue-router';
import UIList from '@/components/ui/list/UIList.vue';
import globalStore, { storeCategoryTreeLens } from '@/store';
import { Tree, ICategory, CategoryQuery, CorePackage, DataBus } from '@findit/core';
import LogoHeader from '@/components/ui/app/LogoHeader.vue';
import ObjectDebug from '@/components/debug/ObjectDebug.vue';
import searchCategoriesByName = CategoryQuery.searchCategoriesByName;
import { VueRouterExt } from '@/router/vue-router-ext';
import Package = CorePackage.Package;
import lift = CorePackage.lift;
import { View } from '@/components/ui/app/view';

export const searchCategoriesResolver = (to: Route, from: Route, next: any) => {
  DataBus.MutableLenses.categoryTree.setM(
    to.meta,
    storeCategoryTreeLens.get(globalStore)
  );
  next();
};

@Component({
  components: {
    LogoHeader,
    UISearchBox,
    UIList,
    ObjectDebug,
  },
  beforeRouteEnter: searchCategoriesResolver,
  beforeRouteUpdate: searchCategoriesResolver,
})
export default class SearchCategoriesView extends View<Package<Tree<ICategory>>> {
    protected search: string = '';

    protected get model(): Package<Tree<ICategory>> {
      return DataBus.GetterLenses.categoryTree.get(this.dataBus) as Package<Tree<ICategory>>;
    }

    protected get categories(): ICategory[] {
      return ((this.search.length > 0)
        ? searchCategoriesByName(lift(this.search), false)(this.model)
        : this.model.map(cs => cs.toArray())
      ).consume();
    }

    protected onSearch(s: string): void {
      this.search = s;
    }
}

export const SearchCategoriesRoute = VueRouterExt.route({
  name: 'search-categories',
  path: '/search',
  component: SearchCategoriesView,
  meta: {
    back: true
  },
});
