import { CoreTypes } from './typelevel';

export namespace CoreFunction {
    import IdentityFn = CoreTypes.IdentityFn;
    import PipeFn = CoreTypes.PipeFn;
    import ComposeFn = CoreTypes.ComposeFn;
    import MapFn = CoreTypes.MapFn;
    import Predicate = CoreTypes.Predicate;

    export const noop: () => void = () => void 0;
    export const identity = <T>(a: T) => a;
    export const pipe: PipeFn = (...fns) => a => fns.reduce((inner, outer) => outer ? outer(inner) : inner, identity(a));
    export const compose: ComposeFn = (...fns) => a => fns.reduceRight((inner, outer) => outer ? outer(inner) : inner, identity(a));
    export const map: <X, Y>(fn: MapFn<X, Y>) => (a: X) => Y = fn => a => fn(a);
    export const tap: <T>(fn: (a: T) => void) => (a: T) => T = fn => a => {
        fn(a);
        return a;
    };

    export const repeatTimes: <T extends any[]>(fn: (a: T) => T, n: number) => (a: T) => T
      = (fn, n) => n > 0 ? compose(fn, repeatTimes(fn, n - 1)) : identity;

    export const repeatWhile: <T extends any[]>(fn: (a: T) => T, cond: () => boolean) => (a: T) => T
      = (fn, cond) => cond() ? compose(fn, repeatWhile(fn, cond)) : identity;

    // export const delay: <T extends any[]>(millis: number) => (fn: (a: T) => T) => Promise<(a: T) => T>
    //   = (millis) => (fn) => asPromise(of(fn).pipe(_delay(millis)));

    export const notEmpty
      : Predicate<any>
      = value => typeof value !== 'undefined' && value !== null;

    export const assertNever
      : (x: never) => never
      = (x) => {
        throw new Error("Unexpected object: " + x);
      };



    type Match<X, Y> = Y | {
        on: (pred: (x: X) => boolean, fn: MapFn<X, Y>) => Match<X, Y>,
        otherwise: (fn: MapFn<X, Y>) => Y
    }
    type MatchFn<X, Y> = (x: X) => Match<X, Y>


// @ts-ignore
    export const matched = (x) => ({
        on: () => matched(x),
        otherwise: () => x,
    });

// @ts-ignore
    export const match = (x) => ({
        // @ts-ignore
        on: (pred, fn) => (pred() ? matched(fn(x)) : match(x)),
        // @ts-ignore
        otherwise: fn => fn(x),
    });
}
