import * as faker from 'faker';
import { CoreRandom } from '../../src/utils/random';
import { Gender, Genders, PersonId } from '../../src/dto/person.dto';
import { IPrivateUser, IPublicUser, IServiceProviderUser } from '../../src/dto/user.dto';
import { IService } from '../../src/dto/service.dto';
import { CoreUuid } from '../../src/utils/uuid';
import { CorePackage } from '../../src/tools/package';
import { CoreTypes } from '../../src/utils/typelevel';
import { CoreEnum } from '../../src/utils/enum';
import { None } from 'space-lift/es';

export module MockUser {
  import randomFrom = CoreRandom.randomFrom;
  import values = CoreEnum.values;
  import lift = CorePackage.lift;
  import flatten3 = CorePackage.flatten3;
  import Package = CorePackage.Package;
  import xRandomFrom = CoreRandom.xRandomFrom;
  import randomBetween = CoreRandom.randomBetween;

  export const toFakerGender
    : (g: Gender) => 0 | 1
    = (g) => (g === 'm') ? 0 : (g === 'f') ? 1 : randomFrom([0, 1], None);

  export const randomGender
    : () => Gender
    = () => faker.random.arrayElement(values(Genders));

  export const randomPublicUser
    : (username?: string) => IPublicUser
    = (username) => lift(username)
      .orElse(() => faker.internet.userName())
      .zipValue(() => faker.random.boolean())
      .map(([u, isServiceProvider]) => ({
        id: CoreUuid.uuid<PersonId>(),
        name: {
          default: u,
          username: u
        },
        isLoggedIn: false as false,
        isServiceProvider: isServiceProvider,
      }))
      .consume();

  export const randomPublicUserList
    : (x: number) => IPublicUser[]
    = x => CorePackage.lift(Array(x).fill(undefined))
      .map(xs => xs.map((_) => randomPublicUser(faker.internet.userName()))).consume();

  export const randomPrivateUser
    : (pu?: IPublicUser) => IPrivateUser
    = pu => flatten3(
        CorePackage.lift(pu).map(publicUser => !!publicUser ? publicUser : randomPublicUser(faker.internet.userName())),
        lift(randomGender()),
        lift(faker.random.boolean)
      ).map(([publicUser, gender, isServiceProvider]) => ({
        ...publicUser,
        name: {
          ...publicUser.name,
          first: faker.name.firstName(toFakerGender(gender)),
          last: faker.name.lastName(toFakerGender(gender)),
        },
        email: faker.internet.email(publicUser.name.username),
        gender: gender,
        password: faker.internet.password(),
        isLoggedIn: true as true,
      }))
      .consume();
}
