import { CoreFunction } from "../utils/function";
import { CoreTypes } from "../utils/typelevel";
import { IPrivateUser, IPublicUser, IServiceProviderUser, IUser } from '../dto/user.dto';
import { PrivatePersonDto } from "../dto/person.dto";
import { IService } from '../dto/service.dto';
import { CorePackage } from '../tools/package';
import { CoreTuple } from '../utils/tuple';
import { ServiceQuery } from './service.queries';

export namespace UserQuery {
    import pipe = CoreFunction.pipe;
    import MapFn = CoreTypes.MapFn;
    import flatten2 = CorePackage.flatten2;
    import lift = CorePackage.lift;
    import zip2 = CoreTuple.zip2;
    import searchServicesByProvider = ServiceQuery.searchServicesByProvider;

    export const privateUserToPublicUser
      : MapFn<IPrivateUser, IPublicUser>
      = u => ({
        ...u,
        isLoggedIn: false,
      });

    export const privatePersonToPublicUser
      : MapFn<PrivatePersonDto, IPublicUser>
      = pipe(privateUserToPublicUser);


    export const userToServiceProvider
      : <U extends IUser>(services: IService[]) => (user: U) => U & IServiceProviderUser
      = services => user => flatten2(
        lift(services).flatMapPkg(
          searchServicesByProvider(lift(user).map(u => u.id))
        ),
        lift(user)
      )
      .map(([ss, u]) => ({
        ...u,
        isServiceProvider: ss.length > 0,
        servicesOffered: ss.map(s => s.id),
      }))
      .consume();

}
