











import { Component, Prop, Vue } from 'vue-property-decorator';
import { component as VueFormGenerator } from 'vue-form-generator';
import "vue-form-generator/dist/vfg.css";
import { CoreForm, CoreFunction, CoreUuid } from '@findit/core';
import uuid = CoreUuid.uuid;
import FieldSchema = CoreForm.FieldSchema;
import Options = CoreForm.Options;
import noop = CoreFunction.noop;
import FieldError = CoreForm.FieldError;
import Field = CoreForm.Field;

type EmptyForm = {};

@Component({
  components: {
    'vue-form-generator': VueFormGenerator,
  }
})
export default class UIForm<Model extends EmptyForm> extends Vue {
  @Prop({ default: `ui-form-${uuid()}` })
  protected id!: string

  @Prop({ default: () => ({}) })
  protected model!: Model;

  @Prop({ default: () => ({}) })
  protected schema!: FieldSchema<Model>;

  @Prop({ default: () => ({}) })
  protected options!: Options;

  @Prop({ default: noop })
  protected onValidated!: (isValid: boolean, errors: FieldError<Model, Field<Model>>[]) => void;

  @Prop({ default: noop })
  protected onChanged!: <Key extends keyof Model>(model: Model[Key], schema: Key) => void;

}
