import { Option, Some } from 'space-lift/es';
import { Set } from 'immutable';
import { CorePackage } from './package';
import { CoreLens } from './lens';
import { CoreTypes } from '../utils/typelevel';
import { CoreNumber } from '../utils/number';
import { CoreBoolean } from '../utils/boolean';
import { CoreSet } from '../utils/set';
import { CoreRandom } from '../utils/random';
import { CoreConfig } from '../utils/config';
import Maybe = CoreTypes.Maybe;
import max = CoreNumber.max;
import MapFn = CoreTypes.MapFn;
import Predicate = CoreTypes.Predicate;
import boolOr = CoreBoolean.boolOr;
import positive = CoreNumber.positive;
import boolAnd = CoreBoolean.boolAnd;
import zero = CoreNumber.zero;
import negative = CoreNumber.negative;
import emptySet = CoreSet.emptySet;
import lift = CorePackage.lift;
import composeG = CoreLens.composeG;
import prism = CoreLens.prism;

export type TreeDto<T> = {
  node: T;
  parent?: T;
  children?: TreeDto<T>[];
}



export class Tree<T> {//implements Named {
  node: Some<T>;
  parent: Option<Tree<T>>;
  children: Option<Set<Tree<T>>>;

  constructor(
    data: T,
    parent?: Tree<T>,
    children?: Set<Tree<T>>,
    // readonly name: Maybe<string> = void 0
  ) {
    this.node = Some(data);
    this.parent = Option(parent);
    this.children = Option(children);
  }

  ancestors
    : (distance?: number, offset?: number) => Set<Tree<T>>
    = (distance = Number.MAX_VALUE, offset = 0) =>
    this.parent
      .filter(_ => boolAnd(positive(distance), boolOr(zero(offset), negative(offset-distance))))
      .map(p => Set([p].concat(...p.ancestors(distance-1, offset))))
      .getOrElse(emptySet());

  siblings
    : () => Set<Tree<T>>
    = () =>
    this.parent
      .map(p => p.children.getOrElse(emptySet()))
      .getOrElse(emptySet());

  descendants
    : (distance?: number, offset?: number) => Set<Tree<T>>
    = (distance = Number.MAX_VALUE, offset = 0) =>
    this.children
      .filter(children => boolAnd(children.count() > 0, distance > 0))
      .map((c) => (offset <= 0 ? c : emptySet<Tree<T>>()).concat(c
        .filter(child => !child.isLeafNode())
        .reduce(
          (p, c, i) => p.concat(c.descendants(distance-1 , offset-1)),
          emptySet<Tree<T>>()
      )))
      .getOrElse(emptySet());

  isLeafNode
    : Predicate<void>
    = () => {
    return this.children.map(c => c.size < 1).getOrElse(true);
  }

  map
    : <U>(fn: MapFn<T, U>) => Tree<U>
    = <U>(fn: MapFn<T, U>) => this.traverseDepthFirst<Tree<U>>(Tree.emptyTree())((([o, n]) => new Tree<U>(
        composeG(Tree.nodeG<T>(), prism(fn)).get(o),
        Tree.parentG<T>().get(o) as any as Tree<U>,
        Set(Tree.childrenG<T>().get(o).map(t => t.map(fn)))
      )));

  traverse
    : <U>(s: Tree.TraverseStrategy) => Tree.TraverseFn<T, U>
    = s => (s === 'BreadthFirst')
      ? this.traverseBreadthFirst.bind(this)
      : this.traverseDepthFirst.bind(this);

  inBreadthFirstOrder
    : () => Tree<T>[]
    = () => lift(this)
      .map(t => {
        let level = 1, offset = 0;
        let queue: Tree<T>[] = [];
        queue.push(t);
        while (offset - level < 0 && level <= t.depth) {
          queue = queue.concat([...t.descendants(level, offset).toArray()]);
          level++;
          offset++;
        }
        return queue;
      })
      .consume();

  traverseBreadthFirst
    : <U>(initial: U) => (fn: MapFn<[Tree<T>, U], U>) => U
    = initial => fn => lift(this)
        .map(t => t.inBreadthFirstOrder())
        .map((q) =>
          q.reduce(((
              previousValue,
              currentValue,
              currentIndex,
              array) =>
              fn([currentValue, previousValue])
          ), initial)
        )
        .consume();

  traverseDepthFirst
    : <U>(initial: U) => (fn: MapFn<[Tree<T>, U], U>) => U
    = initial => fn => lift(this)
      .map((t) => {
        return !t.isLeafNode()
          ? t.traverseChildren(initial)(([n, previous]) =>
            n.traverseDepthFirst(previous)(fn)
          )
          : initial
      })
      .map(value => fn([this, value]))
      .consume();

  traverseChildren
    : <U>(initial: U) => (fn: MapFn<[Tree<T>, U], U>) => U
    = initial => fn => this.children
      .filter(c => c.count() > 0)
      .map(c => c.reduce((p, c, i) => fn([c, p]), initial))
      .get() || initial;

  find
    : (s: Tree.TraverseStrategy) => (fn: Predicate<T>) => Maybe<Tree<T>>
    = s => fn => lift(this)
      .map(t => t.traverse<Maybe<Tree<T>>>(s))
      .map(traverseFn => traverseFn(void 0))
      .map(traverseFn => traverseFn(([n, c]) => {
        return (c !== undefined && c.node.isDefined())
          ? c
          : n.node
            .map(fn)
            .getOrElse(false)
          ? n
          : undefined
      }))
      .consume();

  filter
    : (s: Tree.TraverseStrategy) => (fn: Predicate<T>) => T[]
    = s => fn => lift(this)
      .map(t => t.traverse<T[]>(s))
      .map(traverse => traverse([]))
      .map(traverse => traverse(([n, c]) =>
        (c && n.node.isDefined() && n.node.map(fn).getOrElse(false))
          ? c.concat(n.node.get())
          : c
      ))
      .consume();

  toArray
    : () => T[]
    = () => lift(this)
      .map(t => t.traverse<T[]>('BreadthFirst'))
      .map(traverse => traverse([]))
      .map(traverse => traverse(
        ([n ,c]) =>
          (c && n.node.isDefined())
            ? c.concat(n.node.get())
            : c
      ))
      .consume();

  get depth(): number {
    return lift(this)
      .map(t => t.traverse<number>('DepthFirst')(0)(
        ([n, maxDepth]) => max(n.ancestors().size, maxDepth)))
      .consume();
  }

  get size(): number {
    return this.descendants().size + 1;
  }
}


export namespace Tree {
  import MapFn = CoreTypes.MapFn;
  import isFixedSizeConfig = CoreConfig.isFixedSizeConfig;
  import FixedSizeConfig = CoreConfig.FixedSizeConfig;
  import isMinMaxConfig = CoreConfig.isMinMaxConfig;
  import MinMaxConfig = CoreConfig.MinMaxConfig;
  import Defer = CoreTypes.Defer;
  import Factory = CoreTypes.Factory;
  import Lens = CoreLens.Lens;
  import prism = CoreLens.prism;
  import composeG = CoreLens.composeG;
  import propG = CoreLens.propG;
  import lift = CorePackage.lift;
  import clamp = CoreNumber.clamp;
  import nothing = CoreTypes.nothing;
  import clampLower = CoreNumber.clampLower;
  import randomBetween = CoreRandom.randomBetween;
  import liftOption = CorePackage.liftOption;
  import GetterLens = CoreLens.GetterLens;

  export type TraverseStrategy = 'BreadthFirst' | 'DepthFirst';
  export type TraverseFn<T, U> = (initial: U) => (fn: MapFn<[Tree<T>, U], U>) => U;
  export type TreeGeneratorConfig = (MinMaxConfig | FixedSizeConfig) & {
    levels: number;
  };

  export function emptyTree<T>(): Tree<T> {
    return new Tree<T>({} as T)
  }

  export function nodeG<T>(): GetterLens<Tree<T>, T> {
    return composeG(
      propG('node'),
      prism(b => b.get())
    );
  }
  export function parentG<T>(): GetterLens<Tree<T>, Maybe<Tree<T>>> {
    return composeG(
      propG<Tree<T>, 'parent'>('parent'),
      prism(b => b.get())
    );
  }
  export function childrenG<T>(): GetterLens<Tree<T>, Array<Tree<T>>> {
    return composeG(
      propG('children'),
      prism(b => b.isDefined()
        ? b.get().toArray()
        : [])
    );
  }
  export function descendantsG<T>(): GetterLens<Tree<T>, Array<Tree<T>>> {
    return composeG(
      prism(t => t.descendants()),
      prism(ds => ds.toArray())
    );
  }
  export function ancestorsG<T>(): GetterLens<Tree<T>, Array<Tree<T>>> {
    return composeG(
      prism(t => t.ancestors()),
      prism(ds => ds.toArray())
    );
  }

  export const generateTree
    : <T>(options: TreeGeneratorConfig, parent?: Tree<T>) => (generatorFn: Defer<T>) => Tree<T>
    = <T>(options: TreeGeneratorConfig, parent: Maybe<Tree<T>>) => (generatorFn: Defer<T>) =>
    lift(generatorFn)
      .map(treeLeafNode<T>(parent))
      .tap(populateTree(options)(generatorFn))
      .consume();

  export const treeLeafNode
    : <T>(parent?: Tree<T>) => (generatorFn: Factory<T>) => Tree<T>
    = parent => generatorFn => new Tree(generatorFn(), parent);

  const populateTree
    : <T>(options: TreeGeneratorConfig) => (generatorFn: Defer<T>) => (parent: Tree<T>) => Tree<T>
    = <T>(options: TreeGeneratorConfig) => (generatorFn: Defer<T>) => (parent: Tree<T>) => lift(parent)
    .filter(tree => (!!options.levels && tree.ancestors().size < options.levels))
    .tap((tree) => {
      if (tree !== nothing) {
        tree.children = Option(liftOption(tree.children)
          .map(_ => generateChildren<T>(options)(generatorFn)(parent))
          .consume()
        );
      }
    })
    .orElse(() => parent)
    .consume();

  const generateChildren
    : <T>(options: TreeGeneratorConfig) => (generatorFn: Defer<T>) => (parent: Maybe<Tree<T>>) => Set<Tree<T>>
    = <T>(options: TreeGeneratorConfig) => (generatorFn: Defer<T>) => (parent: Maybe<Tree<T>>) => lift(options)
    .map(options => isFixedSizeConfig(options)
      ? Array(clamp(1, Number.MAX_VALUE)((options as FixedSizeConfig).size))
      : isMinMaxConfig(options)
        ? Array(randomBetween(clampLower(0)((options as MinMaxConfig).min), clampLower(1)((options as MinMaxConfig).max)))
        : []
    )
    .map(array => array.fill(nothing))
    .map(array => array.map(_ => generateTree(options as TreeGeneratorConfig, parent)(generatorFn)))
    .map(array => Set(array))
    .consume();

  export function deserialize<T>(raw: TreeDto<T>, parent?: Tree<T>): Tree<T> {
    return lift(raw)
      .map(treeDto => new Tree<T>(
        treeDto.node,
        parent ? parent : nothing,
      ))
      .tap(tree => {
        if (raw.children) {
          tree.children = Some(Set(raw.children.map(c => deserialize(c, tree))));
        }
      })
      .consume();
  }
}
