import * as faker from "faker";
import { CoreFunction } from '../../src/utils/function';
import { CoreRandom } from '../../src/utils/random';
import { CoreString } from '../../src/utils/string';
import { CoreUuid, ICategory, IPublicUser, IService, CorePackage, ServiceId, PersonId, CategoryId } from '../../src';

export module MockService {

  import pipe = CoreFunction.pipe;
  import randomBetween = CoreRandom.randomBetween;
  import toTagName = CoreString.toTagName;

  export const random
    : (cs: ICategory[]) => (us: IPublicUser[]) => (name?: string) => IService
    = cs => us => name => CorePackage.lift(name)
      .map(n => n || `SERVICE - (${faker.random.words(randomBetween(1, 3))})`)
      .map(n => ({
        id: CoreUuid.uuid<ServiceId>(),
        name: {
          default: n.toLowerCase(),
          full: faker.random.words(randomBetween(3, 10)),
          slug: toTagName(n)
        },
        category: cs.length > 0 ? faker.random.arrayElement(cs).id : CoreUuid.empty<CategoryId>(),
        createdOn: faker.date.recent(randomBetween(1, 356)),
        provider: us.length > 0 ? faker.random.arrayElement(us).id : CoreUuid.empty<PersonId>(),
        rate: randomBetween(5, 50),
      }))
      .consume();

  export const randomList
    : (cs: ICategory[]) => (us: IPublicUser[]) => (x: number) => IService[]
    = cs => us => pipe(
    (x: number) => x > 0 ? Array(x).fill(undefined) : [],
    (list: Array<undefined>) => list.map(
      _ => random(cs)(us)
    )
  );
}
