






















import { Prop, Component, Vue } from 'vue-property-decorator';
import { ObjectDebug } from './object-debug';

@Component({})
export default class ObjectDebugComponent extends Vue {
    @Prop({ default: () => ({}) }) protected readonly model!: Object;
    @Prop({ default: () => 'primary', required: false, type: String }) protected readonly color!: ObjectDebug.Colour;
    @Prop({ default: () => false }) protected readonly simple!: boolean;
    protected readonly switch: 'off' | 'on' = 'on';

    get colourClassModifier(): string {
      return this.color.toString().toLowerCase();
    }
}
