import { Comparable } from '../aspects/comparable';
import { CoreBoolean } from './boolean';
import { CoreRandom } from './random';

export namespace CoreArray {
  import randomBetween = CoreRandom.randomBetween;
  import boolAnd = CoreBoolean.boolAnd;

  export const includes: <T>(search: T, comparer: new (t: T) => Comparable<T>)
    => (list: T[])
    => boolean
    = (search, comparer) =>
    (list) => list.filter((x) => x === search || new comparer(x).equals(search)).length > 0;

  export const includesList: <T>(searches: T[], comparer: new (t: T) => Comparable<T>)
    => (list: T[])
    => boolean
    = (searches, comparer) =>
    (list) => boolAnd(...searches.map(s => includes(s, comparer)(list)));

  export const randomFromArray: <T>(xs: T[]) => T
    = (xs) => xs[randomBetween(0, xs.length - 1)];

  export const randomListFromArray: <T>(x: number) => (xs: T[]) => T[]
    = (x) => (list) => {
    const copy = Object.values(list);
    return Array(x).fill(undefined).map(_ => copy.splice(randomBetween(0, copy.length - 1))[0]);
  };

  export const count: <T extends Comparable<T>>(item: T)
    => (list: T[])
    => number
    = (item) => (list) =>
    list.reduce((c, t) => t.equals(item) ? c + 1 : c, 0);
}
