import { CoreTypes } from '../../src/utils/typelevel';
import { Tree } from '../../src/tools/tree';
import { ObjectPool } from '../../src/tools/object-pool';
import { CorePackage } from '../../src/tools/package';
import { CoreArray } from '../../src/utils/array';
import { IStore } from '../../src/business-layer/store';
import { IPrivateUser, IPublicUser } from '../../src/dto/user.dto';
import { IListing } from '../../src/dto/listing.dto';
import { ITopic } from '../../src/dto/topic.dto';
import { CategoryId, ICategory } from '../../src/dto/category.dto';
import { IService } from '../../src/dto/service.dto';
import { MockUser } from './user';
import { MockCategory } from './category';
import { MockTopic } from './topic';
import { MockListing } from './listing';
import { MockService } from './service';
import { Categories } from '../../src/business-layer/categories';
import { Seq } from 'immutable';
import { CoreUuid } from '../../src/utils/uuid';
import Nothing = CoreTypes.Nothing;
import TObjectPool = ObjectPool.TObjectPool;
import Package = CorePackage.Package;
import nothing = CoreTypes.nothing;
import lift = CorePackage.lift;
import randomFromArray = CoreArray.randomFromArray;
import Set = Seq.Set;
import uuid = CoreUuid.uuid;

export class MockStore implements IStore {
  public readonly user: Package<IPrivateUser>;
  public readonly listings: Package<IListing[]>;
  public readonly topics: Package<ITopic[]>;
  public readonly users: Package<IPublicUser[]>;
  public readonly categories: Package<ICategory[]>;
  public readonly categoryIdTree: Package<Tree<CategoryId>>;
  public readonly services: Package<IService[]>;

  // Simple subjects
  private readonly mockUsers: ObjectPool<IPublicUser,
    Nothing
  >;
  // private readonly mockCategories: ICategory[];
  private readonly mockCategories: ObjectPool<ICategory,
    Nothing
  >;

  // Compound subjects
  private readonly mockTopics: ObjectPool<ITopic, {
    users: TObjectPool<IPublicUser>,
  }>;
  private readonly mockListings: ObjectPool<IListing, {
    users: TObjectPool<IPublicUser>,
  }>;
  private readonly mockServices: ObjectPool<IService, {
    users: TObjectPool<IPublicUser>,
    categories: TObjectPool<ICategory>,
  }>;

  constructor() {
    // const categoryTreeGenerator = generateTree<ICategory>({ max: 15, min: 1, levels: 3 });
    // const categoryTreeGenerator = generateTree<ICategory>({ max: 3, min: 1, levels: 3 });
    // const categoryTreeGenerator = generateTree<ICategory>({ size: Object.keys(CategoryName).length, levels: 1 });
    // const categoryTree = categoryTreeGenerator(() => Categories.map[randomFromArray(Object.keys(CategoryName) as CategoryName[])]);

    const categoryTree = Tree.deserialize(Categories.tree);

    this.mockUsers = new ObjectPool(
      {
        min: 500,
        max: 2500,
        dataSources: nothing,
        create: (i, data) => MockUser.randomPublicUser()
      },
    );

    this.mockCategories = new ObjectPool(
      nothing,
      lift(Object.values(Categories.map).concat(Categories.topCategory)),
    );

    this.mockTopics = new ObjectPool(
      {
        min: 50,
        max: 300,
        dataSources: { users: this.mockUsers },
        create: (i, data) => MockTopic.random(data.users.items)
      },
    );

    this.mockListings = new ObjectPool(
      {
        min: 5,
        max: 15,
        dataSources: { users: this.mockUsers },
        create: (i, data) => MockListing.random(data.users.items)
      },
    );

    this.mockServices = new ObjectPool(
      {
        min: 500,
        max: 2000,
        dataSources: { categories: this.mockCategories, users: this.mockUsers },
        create: (i, data) => {
          return MockService.random(data.categories.items)(data.users.items)()
        }
      }
    );

    console.time('Generating mock store...');

    this.mockUsers.fill();
    // this.mockCategories.fill();
    this.mockTopics.fill();
    this.mockListings.fill();
    this.mockServices.fill();

    this.user = lift(
      MockUser.randomPrivateUser(
        randomFromArray(this.mockUsers.items)
      )
    );
    this.users = lift(this.mockUsers.items);
    this.categories = lift(this.mockCategories.items);
    this.categoryIdTree = lift(categoryTree);
    this.topics = lift(this.mockTopics.items);
    this.listings = lift(this.mockListings.items);
    this.services = lift(this.mockServices.items);

    console.time('Mock stored created.');
    (<any>window).store = this;
  }
}
