












import { Component, Prop, Vue } from 'vue-property-decorator';
import { CoreUuid } from '@findit/core';
import uuid = CoreUuid.uuid;
import UIText from '@/components/ui/text/UIText.vue';

@Component({
  components: {UIText}
})
export default class UICheckbox extends Vue {
  @Prop({ default: `ui-checkbox-${uuid()}` }) protected id!: string;
  @Prop({ default: '' }) protected label!: string;
  @Prop({ default: false }) protected preset!: boolean; // TODO: Hook up to the model

  protected model: boolean = false;
}
