import { BusinessObject, DefaultName, Uuid } from './business-object.dto';
import { CategoryId } from './category.dto';
import { PersonId } from './person.dto';

export type ServiceId = Uuid<'Service'>;

export type ServiceName = DefaultName & {
    full: string;
    slug: string;
}

export type ServiceDto = BusinessObject<ServiceId, ServiceName> & {
    provider: PersonId;
    createdOn: Date;
    expiresOn?: Date;
    category: CategoryId;
    rate: number;
}

export interface IService extends ServiceDto {

}

// TODO Move to separate module
export enum PriceBracket {
    LOW = 1,
    MID = 2,
    HI = 3,
}
export const levelsOfService: PriceBracket[] = [
  PriceBracket.LOW,
  PriceBracket.MID,
  PriceBracket.HI,
];
