import Vue from 'vue';
import Vuex, { Store, ActionContext } from 'vuex';
import {
  CoreLens,
  CREATE_JOB_REQUEST,
  createJobRequestAction,
  CreateJobRequestPayload,
  IStore,
  MockStore
} from '@findit/core';
import propG = CoreLens.propG;
import composeG = CoreLens.composeG;

// TODO: implement basic cloud store
const testStore: IStore = new MockStore();
// const cloudStore: IStore = new CloudStore();

export const storeStateLens           = propG<Store<IStore>, 'state'>('state');
export const storeUserLens            = composeG(storeStateLens, IStore.userLens);
export const storeCategoriesLens      = composeG(storeStateLens, IStore.categoriesLens);
export const storeCategoryIdTreeLens  = composeG(storeStateLens, IStore.categoryIdTreeLens);
export const storeCategoryTreeLens    = composeG(storeStateLens, IStore.categoryTreeLens);
export const storeServicesLens        = composeG(storeStateLens, IStore.servicesLens);
export const storeListingsLens        = composeG(storeStateLens, IStore.listingsLens);
export const storeTopicsLens          = composeG(storeStateLens, IStore.topicsLens);
export const storeUsersLens           = composeG(storeStateLens, IStore.usersLens);

////////////////////////////////////////////////

const inTestMode = true;

Vue.use(Vuex);

export default new Store<IStore>({
  // TODO: Test mode
  // state: (inTestMode) ? testStore : cloudStore,
  state: testStore,
  mutations: {
    [CREATE_JOB_REQUEST]: createJobRequestAction,
  },
  actions: {
    [CREATE_JOB_REQUEST]: (context: ActionContext<IStore, IStore>, payload: CreateJobRequestPayload) => {
      context.commit(CREATE_JOB_REQUEST, payload)
    }
  }
});

