import { CoreTypes } from './typelevel';

export namespace CoreEnum {
  import TypedEnum = CoreTypes.TypedEnum;

  export const typed
    : <T extends string>(...o: T[]) => TypedEnum<T>
    = (...o) => o.reduce((res, key) => {
      res[key] = key;
      return res;
    }, Object.create(null));

  export const values
    : <T extends string>(e: TypedEnum<T>) => T[]
    = <T extends string>(e: TypedEnum<T>) => Object.keys(e) as T[];
}
