import { Option, Some } from "space-lift/es";
import { DefaultComparer } from '../aspects/comparable';
import { CoreArray } from "./array";
import { CoreFunction } from "./function";
import { CoreRandom } from './random';

export namespace CoreNumber {
  import randomBetween = CoreRandom.randomBetween;

  export const zero: (n: number) => boolean = n => !isNaN(n) && n === 0;
  export const positive: (n: number) => boolean = n => !isNaN(n) && n > 0;
  export const negative: (n: number) => boolean = n => !isNaN(n) && n < 0;

  export const range: (a: number, b: number) => number[]
    = (a, b) => (b > a) ? (a < b) ? [a].concat(range(a + 1, b)) : [a] : [];

  export const uniqueBetween: (a: number, b: number, excl: Option<number[]>) => number
    = (a, b, excl) => {
    let x = randomBetween(a, b);
    return (!(excl.isDefined() && CoreArray.includesList([x], DefaultComparer)(excl.get()))) ? x : uniqueBetween(a, b, excl);
  };

  export const uniqueXBetween: (x: number) => (a: number, b: number, excl: Option<number[]>) => number[]
    = (x) => (a, b, excl) =>
    (x > 0)
      ? Some(uniqueXBetween(x - 1)(a, b, excl))
        .map(ls => ls.concat(uniqueBetween(a, b, excl.map(l => l.concat(ls)))))
        .getOrElse([])
      : [];

  export const clampLower: (min: number) => (value: number) => number
    = min => v => v < min ? min : v;

  export const clampUpper: (max: number) => (value: number) => number
    = max => v => v > max ? max : v;

  export const clamp: (min: number, max: number) => (value: number) => number
    = (min, max) => CoreFunction.pipe(
    clampLower(min),
    clampUpper(max)
  );

  export const max: (a: number, b: number) => number
    = ((a, b) => a > b ? a : b);

  export const min: (a: number, b: number) => number
    = ((a, b) => a < b ? a : b);
}
