export namespace CoreTypes {

  export type Nothing = undefined;
  export type Error = never;
  export type Just<T> = T;
  export type Maybe<T> = Just<T> | Nothing;
  export type UnwrappedMaybe<T> = T extends Maybe<infer U> ? U : T;
  export type Nullable<T> = T | null;
  export type Either<A, B> = Just<A> | Just<B>;
  export type Defer<T> = () => T;
  export type Noop = () => void;
  export type GeneratorFn<T> = (t?: Maybe<T>) => T;
  export type MapFn<T, U> = (t: T) => U;
  export type Factory<T> = GeneratorFn<T>;
  export type IdentityFn<T> = MapFn<T, T>;
  export type Predicate<T> = (t: T) => boolean;
  export type Fn<A, B> = Maybe<MapFn<A | any, B | any>>;
  export type Fns<A, B> = Fn<A, B>[];
  export type PipeFn = <A, B>(...fs: Fns<A, B>) => (a?: A) => B;
  export type ComposeFn = <A, B>(...fs: Fns<A, B>) => (a?: B) => A;

  export const nothing: Nothing = void 0;

  export type Tuple2<T, U> = [T, U];
  export type Tuple3<T, U, V> = [T, U, V];
  export type Tuple4<T, U, V, W> = [T, U, V, W];
  export type Tuple5<T, U, V, W, X> = [T, U, V, W, X];
  export type Tuple6<T, U, V, W, X, Y> = [T, U, V, W, X, Y];
  export type Tuple7<T, U, V, W, X, Y, Z> = [T, U, V, W, X, Y, Z];

  export type Functor = {
    map(fn: MapFn<any, any>): Functor;
  }

  export type Applicative = Functor & {
    zip(u: Applicative): Applicative;
  }

  export type Monad = Applicative & {
    flatMap(fn: MapFn<any, Monad>): Monad;
  }

  export type StateMonad<T> = Monad & {
    readonly value: Maybe<Defer<T>>;
  }

  export type IO<T> = Defer<Promise<T>>;

  export type Named = {
    name: Maybe<string>;
  }

  export type Tagged<T> = {
    typeTag: T,
  };

  export type TypedEnum<T extends string> = {[K in T]: K}

  export type Pick<T, U extends keyof T> = { [K in U]: T[K] };
}
