import { CorePackage } from './package';
import { CoreTypes } from '../utils/typelevel';
import { CoreConfig } from '../utils/config';
import { CoreRandom } from '../utils/random';
import Package = CorePackage.Package;
import nothing = CoreTypes.nothing;
import Tuple2 = CoreTypes.Tuple2;

// TODO: Make Object Pool use Observables?
export class ObjectPool<T, DataSources> implements ObjectPool.TObjectPool<T> {

  constructor(
    private readonly config?: ObjectPool.Config<T, DataSources>,
    private value: Package<T[]> = CorePackage.lift([])) {
  }

  get items(): T[] {
    return this.value.consume();
  }

  fill(): Package<T[]> {
    if (this.config !== nothing) {
      this.value = CorePackage.lift(this.value
        .zipValue(() => this.config as ObjectPool.Config<T, DataSources>)
        .map(([v, config]) => [Array(
          (config as CoreConfig.FixedSizeConfig).size
            ? (config as CoreConfig.FixedSizeConfig).size
            : CoreRandom.randomBetween(
            (config as CoreConfig.MinMaxConfig).min, (
              config as CoreConfig.MinMaxConfig).max
            )
          ).fill(void 0), config]
        )
        .map(([v, config]: Tuple2<Array<any>, ObjectPool.Config<T, DataSources>>) => v.map((_, index, array) =>
          config.create(index, config.dataSources)
        ))
        .consume())
    }
    return this.value;
  }

  push(t: T): Package<T[]> {
    return this.value = this.value.map(ls => ls.concat(t));
  }
}

export namespace ObjectPool {
  import Nothing = CoreTypes.Nothing;
  import lift = CorePackage.lift;
  import nothing = CoreTypes.nothing;
  export type TObjectPool<T> = {
    items: T[];
  }

  export type Config<T, U> = CoreConfig.Parameters & {
    dataSources: U,
    create: (i: number, data: U) => T;
  }

  export function fromSource<T>(source: T[]): ObjectPool<T, Nothing> {
    return new ObjectPool<T, Nothing>(nothing, lift(source))
  }
}
