import { CoreTypes } from '../utils/typelevel';

export namespace CoreLens {

  import MapFn = CoreTypes.MapFn;
  export type GetterLens<A, B> = {
    get: (a: A) => B;
  };
  export type SetterLens<A, B> = GetterLens<A, B> & {
    set: (a: A, b: B) => A;
  };
  export type MutableSetterLens<A, B> = SetterLens<A, B> & {
    setM: (a: A, b: B) => A;
  };
  export type Lens<A, B> = MutableSetterLens<A, B> | SetterLens<A, B> | GetterLens<A, B>;
  export type LensDefinition<T, Key extends (keyof T & string), LensType extends Lens<T, T[Key]>> = { [Key in keyof T]: LensType }

  export const isGetter: <A, B>(l: Lens<A, B>) => boolean = t => 'get' in t;
  export const isSetter: <A, B>(l: Lens<A, B>) => boolean = t => 'set' in t;
  export const isMutableSetter: <A, B>(l: Lens<A, B>) => boolean = t => 'setM' in t;

  export function propG<A, P extends keyof A>(p: P): GetterLens<A, A[P]> {
    return {
      get: a => a[p],
    };
  }

  export function propS<A, P extends keyof A>(p: P): SetterLens<A, A[P]> {
    return {
      ...propG<A, typeof p>(p),
      set: (a, b) => ({
        ...a,
        [p]: b
      }),
    };
  }

  export function propM<A, P extends keyof A>(p: P): MutableSetterLens<A, A[P]> {
    return {
      ...propS<A, typeof p>(p),
      setM: (a, b) => {
        a[p] = b;
        return a;
      },
    };
  }

  export function composeG<A, B = A[keyof A], C = B[keyof B]>(l1: Lens<A, B>, l2: Lens<B, C>): GetterLens<A, C> {
    return {
      get: a => l2.get(l1.get(a)),
    };
  }

  export function composeS<A, B = A[keyof A], C = B[keyof B]>(l1: SetterLens<A, B>, l2: SetterLens<B, C>): SetterLens<A, C> {
    return {
      ...composeG(l1, l2),
      set: (a, c) => l1.set(a, l2.set(l1.get(a), c)),
    };
  }

  export function composeM<A, B = A[keyof A], C = B[keyof B]>(l1: MutableSetterLens<A, B>, l2: MutableSetterLens<B, C>): MutableSetterLens<A, C> {
    return {
      ...composeS(l1, l2),
      setM: (a, c) => l1.setM(a, l2.setM(l1.get(a), c)),
    };
  }

  export function and2G<A, B = A[keyof A], C = A[keyof A]>
  (
    l1: Lens<A, B>,
    l2: Lens<A, C>
  )
    : GetterLens<A, [B, C]> {
    return {
      get: a => ([
        l1.get(a),
        l2.get(a),
      ])
    };
  }

  export function and2S<A, B = A[keyof A], C = A[keyof A]>
  (
    l1: SetterLens<A, B>,
    l2: SetterLens<A, C>
  )
    : SetterLens<A, [B, C]> {
    return {
      ...and2G(l1, l2),
      set: (a, [b2, c2]) => l2.set(l1.set(a, b2), c2),
    };
  }

  export function and2M<A, B = A[keyof A], C = A[keyof A]>
  (
    l1: MutableSetterLens<A, B>,
    l2: MutableSetterLens<A, C>
  )
    : MutableSetterLens<A, [B, C]> {
    return {
      get: a => ([
        l1.get(a),
        l2.get(a),
      ]),
      set: (a, [b2, c2]) => l2.set(l1.set(a, b2), c2),
      setM: (a, [b2, c2]) => {
        const a2 = l1.setM(a, b2);
        const a3 = l2.setM(a2, c2);
        return a3;
      },
    };
  }

  export function and3<A,
    B = A[keyof A],
    C = A[keyof A],
    D = A[keyof A],>
  (
    l1: Lens<A, B>,
    l2: Lens<A, C>,
    l3: Lens<A, D>,
  ): GetterLens<A, [B, C, D]> {
    return {
      get: a => ([
        l1.get(a),
        l2.get(a),
        l3.get(a),
      ])
    };
  }

  export function and4<A,
    B = A[keyof A],
    C = A[keyof A],
    D = A[keyof A],
    E = A[keyof A],>
  (
    l1: Lens<A, B>,
    l2: Lens<A, C>,
    l3: Lens<A, D>,
    l4: Lens<A, E>,
  ): GetterLens<A, [B, C, D, E]> {
    return {
      get: a => ([
        l1.get(a),
        l2.get(a),
        l3.get(a),
        l4.get(a),
      ])
    };
  }

  export function and5<A,
    B = A[keyof A],
    C = A[keyof A],
    D = A[keyof A],
    E = A[keyof A],
    F = A[keyof A],>
  (
    l1: Lens<A, B>,
    l2: Lens<A, C>,
    l3: Lens<A, D>,
    l4: Lens<A, E>,
  ): GetterLens<A, [B, C, D, E]> {
    return {
      get: a => ([
        l1.get(a),
        l2.get(a),
        l3.get(a),
        l4.get(a),
      ])
    };
  }

  export function prism<A, B>(m: MapFn<A, B>): GetterLens<A, B> {
    return {
      get: m,
    }
  }
}

